import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import DefaultCreatePage from "../shared/crud/DefaultCreate";
import {invoices} from "../../configurations/navigation";
import DefaultForm from "../shared/crud/DefaultForm";
import InvoiceFormFields, {invoiceValidation} from "./FormFields";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

export default
@inject("invoiceService", "counterpartyService")
@observer
class InvoiceCreatePage extends Component {
	render() {
		const namespace = "invoiceCreatePage";
		const {invoiceService, counterpartyService} = this.props;

		return <DefaultCreatePage
			crudService={invoiceService}
			titleId="components.invoices.create.title"
			crumbs={[invoices]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.invoices.create">
				<FormattedMessage id="tutorials.texts.invoices.create" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultForm
				crudService={invoiceService}
				navConfig={invoices}
				namespace={namespace}
				initialValues={{}}
				validationSchema={invoiceValidation}
			>
				<InvoiceFormFields
					counterpartyService={counterpartyService}
					namespace={namespace}
				/>
			</DefaultForm>
		</DefaultCreatePage>;
	}
}
