import {inject} from "mobx-react";
import React, {Component} from "react";
import {equipmentRepairs} from "../../configurations/navigation";
import {FormattedDate, FormattedMessage, FormattedNumber} from "react-intl";
import DefaultListPage from "../shared/crud/DefaultList";
import DefaultTable from "../shared/crud/DefaultTable";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";
import {defaultDateFormat} from "../../base/configurations/formats";
import {intToCurrencyFormatter} from "../../base/utilities";

export default
@inject("equipmentRepairService", "context")
class EquipmentRepairListPage extends Component {
	render() {
		const namespace = "equipmentRepairListPage";
		const service = this.props.equipmentRepairService;
		const titleId = "components.equipmentRepairs.list.title";
		const searchFields = [
			"description",
			"date",
			"cost",
		];
		const currency = this.props.context.appService.settings.currency;
		const columns = [
			{
				title: () => <FormattedMessage id="components.equipmentRepairs.description"/>,
				dataIndex: "description",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.equipmentRepairs.date"/>,
				dataIndex: "date",
				sorter: true,
				render: (value, record) => <FormattedDate value={new Date(value)} {...defaultDateFormat}/>
			},
			{
				title: () => <FormattedMessage id="components.equipmentRepairs.cost"/>,
				dataIndex: "cost",
				sorter: true,
				render: (value, record) => <FormattedNumber value={intToCurrencyFormatter(value)} format={currency}/>
			},
		];

		return <DefaultListPage
			titleId={titleId}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.equipmentRepairs.list">
				<FormattedMessage id="tutorials.texts.equipmentRepairs.list" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.listPage" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultTable
				namespace={namespace}
				navConfig={equipmentRepairs}
				crudService={service}
				columns={columns}
				searchFields={searchFields}
			/>
		</DefaultListPage>;
	}
}
