import React, {Component} from "react";
import {Helmet} from "react-helmet-async";
import {FlexCol, FlexContainer, FlexRow} from "../base/components/layouts/FlexGrid";
import {FormattedMessage, injectIntl} from "react-intl";
import {formattingFunctions, intlFormats} from "../base/localizations";
import PageTutorial from "../base/components/tutorials/PageTutorial";
import {inject, observer} from "mobx-react";
import {defaultErrorHandler} from "../base/errors/defaultErrorHandler";
import {Card, Icon, Statistic} from "antd";
import {isEmpty, takeRight} from "lodash";
import moment from "moment";
import {Bar, BarChart, CartesianGrid, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis} from "recharts";
import DefaultLoadingPage from "../base/components/data-display/DefaultLoadingPage";
import DefaultEmpty from "../base/components/data-display/DefaultEmpty";
import {intToCurrencyFormatter, paddedNumberFormatter} from "../base/utilities";

export default
@inject("context", "equipmentRepairService")
@injectIntl
@observer
class DashboardPage extends Component {
	namespace = "dashboardPage";

	async componentDidMount() {
		const namespace = this.namespace;
		const {equipmentRepairService} = this.props;

		equipmentRepairService.createNamespace(namespace);

		try {
			await equipmentRepairService.fetchItemList(namespace);
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	}

	render() {
		const namespace = this.namespace;
		const {equipmentRepairService, intl} = this.props;
		const title = intl.formatMessage({id: "components.dashboard.title"});
		const equipmentRepairs = equipmentRepairService.itemList[namespace];
		const currentDate = moment();
		const currentYear = currentDate.year();
		const currentMonth = currentDate.month() + 1;
		const firstDayOfMonthDate = moment().date(1);
		const currentQuarterLabel = `${paddedNumberFormatter(currentMonth - 2, 2)}-${paddedNumberFormatter(currentMonth, 2)}.${currentYear}`;
		const prevQuarterLabel = `${paddedNumberFormatter(currentMonth - 5, 2)}-${paddedNumberFormatter(currentMonth - 3, 2)}.${currentYear}`;

		let monthlyEquipmentSpendItems;
		let yearDiffEquipmentSpend;
		let prev3MonthsDiffEquipmentSpend;
		let currentYearSpend = 0;
		let lastYearSpend = 0;
		let currentQuarterSpend = 0;
		let lastQuarterSpend = 0;

		if (equipmentRepairs && !isEmpty(equipmentRepairs)) {
			monthlyEquipmentSpendItems = takeRight(Object.values(equipmentRepairs.slice().sort((first, second) => moment(first.date).diff(moment(second.date))).reduce((accumulator, currentValue) => {
				const month = intl.formatDate(currentValue.date, {month: "2-digit", year: "numeric"});
				let currentMonthData = accumulator[month];

				if (!currentMonthData) {
					currentMonthData = {month, cost: 0};
					accumulator[month] = currentMonthData;
				}

				currentMonthData.cost += currentValue.cost;

				return accumulator;
			}, {})), 10);

			equipmentRepairs.forEach((repair) => {
				const currentRepairDate = moment(repair.date);
				const year = currentRepairDate.year();
				const monthDiff = moment(currentRepairDate).date(1).diff(firstDayOfMonthDate, "month");

				if (currentYear === year) {
					currentYearSpend += repair.cost;
				}

				if (currentYear - 1 === year) {
					lastYearSpend += repair.cost;
				}

				if (monthDiff <= 0 && monthDiff + 3 > 0) {
					currentQuarterSpend += repair.cost;
				}

				if (monthDiff <= -3 && monthDiff > -6) {
					lastQuarterSpend += repair.cost;
				}
			});

			yearDiffEquipmentSpend = lastYearSpend - currentYearSpend;
			prev3MonthsDiffEquipmentSpend = lastQuarterSpend - currentQuarterSpend;
		}

		return <FlexContainer fluid>
			<Helmet>
				<title>{title} - {this.props.intl.formatMessage({id: "application.meta.title"})}</title>
			</Helmet>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.dashboard">
				<FormattedMessage id="tutorials.texts.dashboard" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<FlexRow>
				<FlexCol>
					<h1>{title}</h1>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol lg={12} xxxl={8} xxxxl={6}>
					<Card title={<FormattedMessage id="components.dashboard.equipmentRepairs.monthlySpend.title"/>} bordered={false}>
						<FlexRow>
							<FlexCol style={{minHeight: "350px"}}>
								{isEmpty(monthlyEquipmentSpendItems) && !equipmentRepairService.isLoadingNamespace(namespace) && <DefaultEmpty/>}
								{isEmpty(monthlyEquipmentSpendItems) && equipmentRepairService.isLoadingNamespace(namespace) && <DefaultLoadingPage/>}
								{!isEmpty(monthlyEquipmentSpendItems) &&
								<ResponsiveContainer>
									<BarChart
										data={monthlyEquipmentSpendItems}
									>
										<CartesianGrid stroke="#677" strokeDasharray="4 4"/>
										<YAxis
											tick={{
												fill: "#ffffff",
											}}
											dataKey="cost"
											tickFormatter={this.formatCurrency}
										/>
										<XAxis
											tick={{
												fill: "#ffffff",
											}}
											dataKey="month"
											// tickFormatter={this.formatDate}
										/>
										<Tooltip
											cursor={{
												fill: "#00000030"
											}}
											contentStyle={{
												backgroundColor: "rgba(10, 47, 42, 0.7)",
												border: "none",
												WebkitBackdropFilter: "blur(16px)",
												backdropFilter: "blur(16px)",
											}}
											itemStyle={{
												color: "#d6fd38",
											}}
											// labelFormatter={this.formatDate}
											formatter={(val, name) => [this.formatCurrency(val), this.formatLabel(name)]}/>
										<Legend formatter={this.formatLabel}/>
										<Bar dataKey="cost" fill="#dfab31"/>
									</BarChart>
								</ResponsiveContainer>
								}
							</FlexCol>
						</FlexRow>
					</Card>
				</FlexCol>
				<FlexCol lg={12} xxxl={8} xxxxl={6}>
					<FlexRow>
						<FlexCol>
							<Card title={<FormattedMessage id="components.dashboard.equipmentRepairs.comparisons.title"/>} bordered={false}>
								<FlexRow>
									<FlexCol>
										<Statistic
											title={intl.formatMessage({id: "components.dashboard.equipmentRepairs.spendingComparedTo"}, {
												current: currentYear,
												previous: currentYear - 1
											})}
											value={this.formatCurrency(yearDiffEquipmentSpend)}
											valueStyle={{color: yearDiffEquipmentSpend > 0 ? '#74fe38' : '#cf1322'}}
											prefix={yearDiffEquipmentSpend > 0 ? <Icon type="arrow-down"/> : <Icon type="arrow-up"/>}
										/>
									</FlexCol>
									<FlexCol>
										<Statistic
											title={intl.formatMessage({id: "components.dashboard.equipmentRepairs.spendingComparedTo"}, {
												current: currentQuarterLabel,
												previous: prevQuarterLabel,
											})}
											value={this.formatCurrency(prev3MonthsDiffEquipmentSpend)}
											valueStyle={{color: prev3MonthsDiffEquipmentSpend > 0 ? '#74fe38' : '#cf1322'}}
											prefix={prev3MonthsDiffEquipmentSpend > 0 ? <Icon type="arrow-down"/> : <Icon type="arrow-up"/>}
										/>
									</FlexCol>
								</FlexRow>
							</Card>
						</FlexCol>
						<FlexCol>
							<Card title={<FormattedMessage id="components.dashboard.equipmentRepairs.totals.title"/>} bordered={false}>
								<FlexRow>
									<FlexCol>
										<Statistic value={this.formatCurrency(currentYearSpend)} suffix={`for ${currentYear}`}/>
									</FlexCol>
									<FlexCol>
										<Statistic value={this.formatCurrency(lastYearSpend)} suffix={`for ${currentYear - 1}`}/>
									</FlexCol>
								</FlexRow>
								<FlexRow className="mt-4">
									<FlexCol>
										<Statistic value={this.formatCurrency(currentQuarterSpend)} suffix={`for ${currentQuarterLabel}`}/>
									</FlexCol>
									<FlexCol>
										<Statistic value={this.formatCurrency(lastQuarterSpend)} suffix={`for ${prevQuarterLabel}`}/>
									</FlexCol>
								</FlexRow>
							</Card>
						</FlexCol>
					</FlexRow>
				</FlexCol>
			</FlexRow>
		</FlexContainer>;
	}

	formatLabel = (label) => {
		const {intl} = this.props;

		return intl.formatMessage({id: `components.equipmentRepairs.${label}`});
	};

	formatCurrency = (val) => {
		const {context, intl} = this.props;
		const currency = context.appService.settings.currency;
		const absVal = Math.abs(val);

		let result = intl.formatNumber(intToCurrencyFormatter(absVal), intlFormats.number[currency]);

		if (absVal >= 100000) {
			result = result.replace(/[0-9]+[0-9., \-_]+/g, `${intl.formatNumber(intToCurrencyFormatter(absVal / 1000), intlFormats.number.decimal)}k`);
		}

		return result;
	};

	formatDate = (dateString) => {
		const {intl} = this.props;

		return intl.formatDate(moment(dateString).toDate());
	};
}
