import {action, observable} from "mobx";
import BaseService from "./BaseService";

/**
 * Generic service capable of handling all CRUD operations. Extend if needed.
 */
export default class DefaultUiService extends BaseService {

	/**
	 * Global properties for all modals. Grouped by namespace -> modal name.
	 * @type {{}}
	 */
	@observable
	modals = {};

	@action
	setModal = (namespace, modalName, modal) => {
		if (!this.modals[namespace]) {
			this.modals[namespace] = {};
		}

		this.modals[namespace][modalName] = {...modal};
	};
}
