import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import {FormattedMessage, injectIntl} from "react-intl";
import {rules} from "../../base/utilities/forms";
import {FormItemInput, FormItemInputNumber} from "../../base/components/inputs";
import {intToCurrencyFormatter, intToCurrencyParser} from "../../base/utilities";
import {FlexCol, FlexRow} from "../../base/components/layouts/FlexGrid";
import * as Yup from "yup";

export const invoiceLineValidation = Yup.object().shape({
	description: rules.required,
	measure: rules.required,
	quantity: rules.required,
	unitPrice: rules.required,
});

export default
@inject("context")
@injectIntl
@observer
class InvoiceLineFormFields extends Component {
	render() {
		return <>
			<FlexRow>
				<FlexCol>
					<FormItemInput
						id="description"
						label={<FormattedMessage id="components.invoiceLines.description"/>}
					/>
				</FlexCol>
				<FlexCol>
					<FormItemInput
						id="measure"
						label={<FormattedMessage id="components.invoiceLines.measure"/>}
					/>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol>
					<FormItemInputNumber
						id="quantity"
						label={<FormattedMessage id="components.invoiceLines.quantity"/>}
					/>
				</FlexCol>
				<FlexCol>
					<FormItemInputNumber
						id="unitPrice"
						label={<FormattedMessage id="components.invoiceLines.unitPriceNoVat"/>}
						formatter={value => this.props.intl.formatNumber(intToCurrencyFormatter(value), {format: "decimal"})}
						parser={value => intToCurrencyParser(value, this.props.context.appService.settings.locale)}
						addonBefore={this.props.context.appService.settings.currency}
					/>
				</FlexCol>
			</FlexRow>
		</>;
	}
};
