import React, {Component} from "react";
import {Link} from "react-router-dom";
import PropTypes from "prop-types";
import {Icon} from "antd";
import classNames from "classnames";

export default class DefaultLogoutButton extends Component {
	static propTypes = {
		iconProps: PropTypes.object,
		logoutUrl: PropTypes.string,
	};

	static defaultProps = {
		logoutUrl: "/logout",
	};

	render() {
		const {logoutUrl, iconProps, className, ...rest} = this.props;
		return <Link className={classNames("logout", {className})} to={logoutUrl} {...rest}><Icon type="poweroff" {...iconProps}/></Link>;
	}
}
