import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import {Card} from "antd";
import DefaultForm from "../shared/crud/DefaultForm";
import {defaultErrorHandler} from "../../base/errors/defaultErrorHandler";
import CompanyDetailsEditFormFields, {companyDetailsValidation} from "./FormFields";
import {FormattedMessage, injectIntl} from "react-intl";
import DefaultBreadcrumbs from "../../base/components/layouts/DefaultBreadcrumbs";
import {dashboard} from "../../configurations/navigation";
import {FlexCol, FlexContainer, FlexRow} from "../../base/components/layouts/FlexGrid";
import DefaultLoadingPage from "../../base/components/data-display/DefaultLoadingPage";
import {Helmet} from "react-helmet-async";
import PageTutorial from "../../base/components/tutorials/PageTutorial";
import {formattingFunctions} from "../../base/localizations";

export default
@inject("companyService")
@withRouter
@injectIntl
@observer
class CompanyDetailsEditPage extends Component {

	namespace = "companyDetailsEditPage";
	itemId = 1;

	async componentDidMount() {
		const {itemId, namespace} = this;
		const {companyService} = this.props;

		try {
			let companyExists = await companyService.existsById(itemId, namespace);

			if (companyExists) {
				await companyService.fetchItem(itemId, namespace);
			} else {
				companyService.setItem({id: itemId}, namespace);
			}
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	}

	componentWillUnmount() {
		this.props.companyService.resetNamespaceData(this.props.namespace);
	}

	render() {
		const namespace = this.namespace;
		const itemId = this.itemId;
		const {form, companyService} = this.props;

		let record = companyService.item[namespace];

		if (!record) {
			return <DefaultLoadingPage/>;
		}

		const title = this.props.intl.formatMessage({id: "components.companyDetails.edit.title"}, {name: record.name});


		let initialValues = {};

		if (record) {
			initialValues = {
				logo: record.logo,
				name: record.name,
				city: record.city,
				address: record.address,
				accountablePersonTitle: record.accountablePersonTitle,
				accountablePerson: record.accountablePerson,
				uic: record.uic,
				vatNumber: record.vatNumber,
				bankName: record.bankName,
				bankBic: record.bankBic,
				bankIban: record.bankIban,
				vatPercentage: record.vatPercentage,
			};
		}


		return <FlexContainer>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.companyDetails.edit">
				<FormattedMessage id="tutorials.texts.companyDetails.edit" values={formattingFunctions} />
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions} />
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions} />
			</PageTutorial>
			<Helmet>
				<title>{title} - {this.props.intl.formatMessage({id: "application.meta.title"})}</title>
			</Helmet>
			<FlexRow>
				<FlexCol>
					<DefaultBreadcrumbs crumbs={[dashboard, {title}]}/>
					<Card style={{margin: "2rem 0"}} title={title} bordered={false}>
						<DefaultForm
							crudService={companyService}
							form={form}
							itemId={itemId}
							namespace={namespace}
							initialValues={initialValues}
							validationSchema={companyDetailsValidation}
							backButtonUrl={dashboard.href}
							redirectAfterSubmit={(id) => {
								this.props.history.replace(this.props.location.pathname);
							}}
						>
							<CompanyDetailsEditFormFields/>
						</DefaultForm>
					</Card>
				</FlexCol>
			</FlexRow>
		</FlexContainer>;
	}
}
