import {action, computed, observable} from "mobx";

/**
 * Base service for all services to extend. Adds the "loading" functionality and holds reference to ApplicationContext.
 */
export default class BaseService {

	/**
	 * This is the application context.
	 */
	context;

	/**
	 * Fetch requests, local storage queries, etc. being executed.
	 * @type {Array}
	 */
	@observable
	beingLoaded = [];

	init = () => {
	};

	setContext = (context) => {
		this.context = context;
	};

	/**
	 * Something started loading
	 * @param loading
	 */
	@action
	loading = (loading) => {
		let isPresent = this.beingLoaded.indexOf(loading) > -1;

		if (isPresent) {
			// console.trace(`Trying to add ${loading} multiple times...`);
			console.log(`Trying to add ${loading} multiple times...`);
		}

		this.beingLoaded.push(loading);
	};

	/**
	 * Something finished loading
	 * @param loaded
	 */
	@action
	loaded = (loaded) => {
		const deleteMe = this.beingLoaded.indexOf(loaded);
		if (deleteMe > -1) {
			this.beingLoaded.splice(deleteMe, 1);
		}
	};

	@computed
	get isLoading() {
		return this.beingLoaded.length > 0;
	}

	isLoadingAnyOf(...loaders) {
		const count = loaders.length;

		for (let i = 0; i < count; i++) {
			if (this.beingLoaded.indexOf(loaders[i]) > -1) {
				return true;
			}
		}

		return false;
	}

	isLoadingNamespace(namespace) {
		for (let i = 0; i < this.beingLoaded.length; i++) {
			if (this.beingLoaded[i].indexOf(namespace) > -1) {
				return true;
			}
		}

		return false;
	}
}
