import {assign, isArray, isEmpty, isNil, isString, startsWith, trim} from "lodash";
import CrudService from "./CrudService";
import {isMoment} from "moment";
import ApplicationError from "../errors/ApplicationError";
import {toJS} from "mobx";

/**
 * Generic service capable of handling all CRUD operations. Extend if needed.
 * This service works with LocalStorage.
 */
export default class LocalStorageCrudService extends CrudService {

	constructor({resourceUrl, ...rest}) {
		const resourcePath = trim(resourceUrl.replace(/\//g, "."), ".");
		super({resourceUrl: resourcePath, ...rest});
	}

	/**
	 * Check if item exists by passed id.
	 * @param id
	 * @param namespace is the namespace of the component requesting this item.
	 */
	existsById = async (id, namespace) => {
		this.loading(`${namespace}:exists:${id}`);

		try {
			const path = `${this.resourceUrl}.${id}`;

			const response = await this.context.localStorageService.load(path);
			return !isEmpty(response);
		} finally {
			this.loaded(`${namespace}:exists:${id}`);
		}
	};

	/**
	 * Check how many records are there currently.
	 * @param namespace is the namespace of the component requesting this item.
	 */
	count = async (namespace) => {
		this.loading(`${namespace}:count`);

		try {
			return (await this.context.localStorageService.keys())
				.filter(key => startsWith(key, this.resourceUrl))
				.length;
		} finally {
			this.loaded(`${namespace}:count`);
		}
	};

	/**
	 * Load single item by passed id.
	 * @param id
	 * @param namespace is the namespace of the component requesting this item.
	 * @returns {Promise<void>}
	 */
	fetchItem = async (id, namespace) => {
		this.loading(`${namespace}:fetch:${id}`);

		try {
			const path = `${this.resourceUrl}.${id}`;

			const response = await this.context.localStorageService.load(path);

			this.setItem(response, namespace);
		} finally {
			this.loaded(`${namespace}:fetch:${id}`);
		}
	};

	/**
	 * Load list of items. Takes into account sorter, filter, search and pagination.
	 * @returns {Promise<void>}
	 */
	fetchItemList = async (namespace) => {
		this.loading(`${namespace}:fetch:list`);

		try {
			const allItems = [];
			const filters = this.filters[namespace];
			const search = this.search[namespace];
			const sortField = this.sorter[namespace].field;
			const sortOrder = this.sorter[namespace].order;
			const currentPage = this.pagination[namespace].current || 0;
			const pageSize = this.pagination[namespace].pageSize || 10;

			await this.context.localStorageService.iterate((itemValue, itemKey) => {
				if (!startsWith(itemKey, this.resourceUrl)) {
					return;
				}

				let passedFilters = true;

				Object.entries(filters).forEach(([filterKey, filter]) => {
					switch (filter.type) {
						case ">x<":
							if (itemValue[filter.field] < filter.values[0] ||
								itemValue[filter.field] > filter.values[1]) {
								passedFilters = false;
							}
							break;
						case "=":
							if (itemValue[filter.field] !== filter.values[0]) {
								passedFilters = false;
							}
							break;
						default:
							console.log(`Unknown filter type: ${filter.type}`);
							break;
					}
				});

				if (!passedFilters) {
					return;
				}

				if (isArray(search.fields)) {
					let passedSearch = false;
					const searchVal = search.value;
					search.fields.forEach(fieldName => {
						let fieldVal = itemValue[fieldName];

						if (isNil(fieldVal)) {
							return;
						}

						if (isMoment(fieldVal)) {
							fieldVal = fieldVal.toISOString();
						}

						if (!isString(fieldVal)) {
							fieldVal = fieldVal.toString();
						}

						if (fieldVal.toLowerCase().indexOf(searchVal.toLowerCase()) > -1) {
							passedSearch = true;
						}
					});

					if (!passedSearch) {
						return;
					}
				}

				allItems.push(itemValue);
			});

			allItems.sort((first, second) => {
				if (first[sortField] < second[sortField]) {
					return sortOrder === "ascend" ? -1 : 1;
				}
				if (first[sortField] > second[sortField]) {
					return sortOrder === "ascend" ? 1 : -1;
				}

				return 0;
			});

			let totalCount = allItems.length;

			allItems.splice(0, (currentPage - 1) * pageSize);
			allItems.splice((currentPage + 1) * pageSize);

			this.setPagination({...this.pagination[namespace], total: totalCount}, namespace);
			this.setItemList(allItems, namespace);
		} finally {
			this.loaded(`${namespace}:fetch:list`);
		}
	};

	/**
	 * Load map of items from a list of ids.
	 * @returns {Promise<void>}
	 */
	fetchItemMap = async (ids, namespace) => {
		this.loading(`${namespace}:fetch:map:${ids.join(",")}`);

		try {
			let itemsArr = await Promise.all(ids.map(id => {
				return this.context.localStorageService.load(`${this.resourceUrl}.${id}`);
			}));

			this.setItemMap(itemsArr, namespace);
		} finally {
			this.loaded(`${namespace}:fetch:map:${ids.join(",")}`);
		}
	};

	/**
	 * Save (create or update) a single item.
	 * @param item
	 * @param namespace
	 * @returns {Promise<void>}
	 */
	save = async (item, namespace) => {
		let id = item[this.primaryKeyField] || "";

		const loadingKey = `${namespace}:save:${id}`;

		this.loading(loadingKey);

		try {
			if (!id) {
				id = await this.count(namespace) + 1;
			}

			await this.context.localStorageService.save(`${this.resourceUrl}.${id}`, assign({}, this.item[namespace], toJS(item), {id}));

			return id;
		} finally {
			this.loaded(loadingKey);
		}
	};

	/**
	 * Delete a single item.
	 * @param id
	 * @param namespace
	 * @returns {Promise<void>}
	 */
	delete = async (id, namespace) => {
		this.loading(`${namespace}:delete:${id}`);

		try {
			await this.context.localStorageService.delete(`${this.resourceUrl}.${id}`);
		} finally {
			this.loaded(`${namespace}:delete:${id}`);
		}
	};

	/**
	 * Adds sorting and ordering parts to a passed URL.
	 * @param url
	 * @param field
	 * @param order
	 */
	appendSortAndOrderToUrl = (url, field, order) => {
		throw new ApplicationError("errors.unsupportedMethod");
	};

	/**
	 * Adds searching parts to a passed URL.
	 * @param url
	 * @param search
	 */
	appendSearchToUrl = (url, search) => {
		throw new ApplicationError("errors.unsupportedMethod");
	};

	/**
	 * Adds pagination to a passed URL.
	 * @param url
	 * @param pagination
	 */
	appendPaginationToUrl = (url, pagination) => {
		throw new ApplicationError("errors.unsupportedMethod");
	};

	/**
	 * Adds list of ids to passed URL.
	 * @param url
	 * @param ids
	 */
	appendIdsToUrl = (url, ids) => {
		throw new ApplicationError("errors.unsupportedMethod");
	};
}
