import React, {Component} from "react";
import DefaultLoginPage from "../../base/components/auth/DefaultLoginPage";
import {inject, observer} from "mobx-react";
import {defaultErrorHandler} from "../../base/errors/defaultErrorHandler";
import {companies, counterparties, equipmentRepairs, invoiceLines, invoices, users} from "../../configurations/demo-data";
import DefaultLoadingPage from "../../base/components/data-display/DefaultLoadingPage";

export default
@inject("context")
@observer
class LoginPage extends Component {
	namespace = "loginPage";

	async componentDidMount() {
		const {context} = this.props;

		try {
			const hasUsers = await context.services.userService.count() > 0;

			if (!hasUsers) {
				await Promise.all([
					...this.generateDemoData(users, context.services.userService),
					...this.generateDemoData(companies, context.services.companyService),
					...this.generateDemoData(counterparties, context.services.counterpartyService),
					...this.generateDemoData(invoices, context.services.invoiceService),
					...this.generateDemoData(invoiceLines, context.services.invoiceLineService),
					...this.generateDemoData(equipmentRepairs, context.services.equipmentRepairService),
				]);
			}
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	}

	render() {
		if (this.props.context.services.userService.isLoadingNamespace(this.namespace)) {
			return <DefaultLoadingPage/>;
		}

		return <DefaultLoginPage/>;
	}

	generateDemoData = (data, service) => {
		return data.map(record => {
			return service.save(record, this.namespace);
		});
	};
}
