import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import {counterparties} from "../../configurations/navigation";
import DefaultEditPage from "../shared/crud/DefaultEdit";
import DefaultForm from "../shared/crud/DefaultForm";
import CounterpartyFormFields, {counterpartyValidation} from "./FormFields";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

export default
@inject("counterpartyService")
@withRouter
@observer
class CounterpartyEditPage extends Component {
	render() {
		const namespace = "counterpartyEditPage";
		const itemId = parseInt(this.props.match.params.counterpartyId, 10);
		const {counterpartyService} = this.props;
		let record = counterpartyService.item[namespace];

		let initialValues = {};
		let titleParams = {};

		if (record) {
			initialValues = {
				company: record.company,
				fromEu: record.fromEu,
				address: record.address,
				accountablePersonTitle: record.accountablePersonTitle,
				accountablePerson: record.accountablePerson,
				uic: record.uic,
				vatNumber: record.vatNumber,
			};

			titleParams = {
				name: record.company
			};
		}

		return <DefaultEditPage
			crudService={counterpartyService}
			titleId="components.counterparties.edit.title"
			titleParams={titleParams}
			itemId={itemId}
			crumbs={[counterparties]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.counterparties.edit">
				<FormattedMessage id="tutorials.texts.counterparties.edit" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultForm
				crudService={counterpartyService}
				navConfig={counterparties}
				itemId={itemId}
				namespace={namespace}
				initialValues={initialValues}
				validationSchema={counterpartyValidation}>
				<CounterpartyFormFields/>
			</DefaultForm>
		</DefaultEditPage>;
	}
}
