import React, {Component} from 'react';
import {Link, withRouter} from "react-router-dom";
import {Icon, Menu} from "antd";
import {inject, observer} from "mobx-react";
import PropTypes from "prop-types";
import {FormattedMessage} from "react-intl";
import {isEmpty} from "lodash";

/**
 * Default menu.
 */
@inject("context")
@withRouter
@observer
class DefaultMenu extends Component {
	static propTypes = {
		menuTheme: PropTypes.string.isRequired,
		menuMode: PropTypes.string.isRequired,
		menuProps: PropTypes.object,
		menuItemProps: PropTypes.object,
	};

	static defaultProps = {
		menuProps: {},
		menuItemProps: {},
		menuTheme: "dark",
		menuMode: "inline",
	};

	render() {
		const {menuTheme, menuMode, menuProps, menuItemProps} = this.props;
		const path = this.props.location.pathname;
		const service = this.props.context.appService;

		const menuItems = service.sidebarMenuItems;

		const menuSelectedKeys = [];
		menuItems.forEach(item => {
			if (path.indexOf(item.href) === 0) {
				menuSelectedKeys.push(item.id);
			}
		});

		return <Menu theme={menuTheme} mode={menuMode} selectedKeys={menuSelectedKeys} {...menuProps}>
			{this.renderMenuItems({menuItems, menuItemProps})}
		</Menu>;
	}

	renderMenuItems = ({menuItems, menuItemProps}) => {
		const service = this.props.context.appService;

		return menuItems.map(item => {
			if (item.auth && !service.isAuthenticated) {
				return null;
			}

			if (!isEmpty(item.items)) {
				return <Menu.SubMenu key={item.id} title={this.renderMenuItemLabel(item)}>
					{this.renderMenuItems({menuItems: item.items, menuItemProps})}
				</Menu.SubMenu>
			}

			return <Menu.Item key={item.id} {...menuItemProps}>
				<Link to={item.href}>
					{this.renderMenuItemLabel(item)}
				</Link>
			</Menu.Item>;
		});
	};

	renderMenuItemLabel = (item) => {
		return <>{item.icon && <Icon type={item.icon}/>}<FormattedMessage id={item.titleId}/></>;
	};
}

export default DefaultMenu;
