import {padStart, trimStart} from "lodash";
import Decimal from "decimal.js";

const parseLocaleNumber = (stringNumber, locale) => {
	let thousandSeparator = (new Intl.NumberFormat(locale, {})).format(1111).replace(/1/g, '');

	let decimalSeparator = (new Intl.NumberFormat(locale, {
		minimumFractionDigits: 1,
		maximumFractionDigits: 1
	})).format(1.1).replace(/1/g, '');

	let result = stringNumber;

	if (thousandSeparator && thousandSeparator.length > 0) {
		result = result.replace(new RegExp('\\' + thousandSeparator, 'g'), '');
	}

	if (decimalSeparator && decimalSeparator.length > 0) {
		result = result.replace(new RegExp('\\' + decimalSeparator), '.');
	}

	result = result.replace(/[^\d.-]/g, '').split(".").filter(el => !!el);

	let first = result.shift();
	let rest = result.join("");

	if (result.length > 0) {
		return [first, rest].join(".");
	} else {
		return first;
	}
};

export const isNumeric = (n) => {
	return !isNaN(parseFloat(n)) && isFinite(n);
};

export const paddedNumberFormatter = (number, length = 10, chars = "0") => (padStart(number, length, chars));
export const paddedNumberParser = (number, chars = "0") => (trimStart(number, chars));
export const intToCurrencyFormatter = (number) => {
	if (isNumeric(number)) {
		return new Decimal(number).div(100).toFixed(2);
	}
	return 0;
};
export const intToCurrencyParser = (rawNumber, locale) => {
	if (!rawNumber) {
		return 0;
	}

	let number = rawNumber;
	let decimalPlaces = 1;

	if (!isNumeric(number)) {
		number = parseLocaleNumber(number, locale);
	}

	while (number.charAt(number.length - decimalPlaces) === "0") {
		decimalPlaces++;
	}

	decimalPlaces--;

	let dNumber = new Decimal(number);
	decimalPlaces += dNumber.decimalPlaces();

	dNumber = Decimal.mul(dNumber, Math.pow(10, decimalPlaces));

	return dNumber.toNumber();
};
