import React, {Component} from "react";
import {observer} from "mobx-react";
import {Card} from "antd";
import {injectIntl} from "react-intl";
import DefaultBreadcrumbs from "../../../base/components/layouts/DefaultBreadcrumbs";
import {dashboard, defaultUrl} from "../../../configurations/navigation";
import PropTypes from "prop-types";
import CrudService from "../../../base/services/CrudService";
import DefaultLoadingPage from "../../../base/components/data-display/DefaultLoadingPage";
import {defaultErrorHandler} from "../../../base/errors/defaultErrorHandler";
import {FlexCol, FlexContainer, FlexRow} from "../../../base/components/layouts/FlexGrid";
import {Redirect} from "react-router-dom";
import {Helmet} from "react-helmet-async";

export default @injectIntl
@observer
class DefaultEditPage extends Component {
	static propTypes = {
		itemId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
		crudService: PropTypes.instanceOf(CrudService).isRequired,
		titleId: PropTypes.string.isRequired,
		titleParams: PropTypes.object.isRequired,
		crumbs: PropTypes.array.isRequired,
		namespace: PropTypes.string.isRequired,
	};

	static defaultProps = {
		crumbs: [],
		titleParams: {},
	};

	constructor(props) {
		super(props);

		this.state = {
			mounted: false
		};
	}

	async componentDidMount() {
		const {itemId, namespace} = this.props;

		try {
			await this.props.crudService.fetchItem(itemId, namespace);
			this.setState({mounted: true});
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	}

	componentWillUnmount() {
		this.props.crudService.resetNamespaceData(this.props.namespace);
	}

	render() {
		const {titleId, crudService, crumbs, namespace} = this.props;

		let record = crudService.item[namespace];

		if (!this.state.mounted || crudService.isLoadingNamespace(namespace)) {
			return <DefaultLoadingPage/>;
		}

		if (!record) {
			return <Redirect to={defaultUrl}/>;
		}

		const title = this.props.intl.formatMessage({id: titleId}, this.props.titleParams);

		return <FlexContainer>
			<Helmet>
				<title>{title} - {this.props.intl.formatMessage({id: "application.meta.title"})}</title>
			</Helmet>
			<FlexRow>
				<FlexCol>
					<DefaultBreadcrumbs crumbs={[dashboard, ...(crumbs), {title}]}/>
					<Card style={{margin: "2rem 0"}} title={title} bordered={false}>
						{this.props.children}
					</Card>
				</FlexCol>
			</FlexRow>
		</FlexContainer>;
	}
}
