import {isPlainObject, merge, startsWith, trim} from "lodash";
import HTTPError from "../errors/HTTPError";
import {toJS} from "mobx";
import ApplicationError from "../errors/ApplicationError";
import DefaultApplicationService from "./DefaultApplicationService";

export default class LocalStorageApplicationService extends DefaultApplicationService {

	/**
	 * Exchanges credentials for access_token.
	 * @param username
	 * @param password
	 * @returns {Promise<void>}
	 */
	login = async (username, password) => {
		this.loading("application:user:login");

		try {
			await this.context.localStorageService.iterate((value, key) => {
				if (!startsWith(key, "api.users")) {
					return;
				}

				if (value.username === username && value.password === password) {
					this.setOAuthData({
						access_token: `${value.username}:${value.password}`
					});
				}
			});

			await this.whoAmI();
			this.context.localStorageService.save("oAuthData", toJS(this.oAuthData));
		} finally {
			this.loaded("application:user:login");
		}
	};

	/**
	 * Loads the user profile.
	 * @returns {Promise<void>}
	 */
	whoAmI = async () => {
		this.loading("application:user:whoAmI");

		try {
			if (!this.hasToken) {
				throw new HTTPError({status: 400});
			}

			const [username, password] = this.oAuthData.access_token.split(":");
			let user = null;

			await this.context.localStorageService.iterate((value) => {
				if (value.username === username && value.password === password) {
					user = value;
				}
			});

			if (user) {
				const {password, ...profile} = user;

				this.setUserProfile(profile);
			} else {
				this.logout();
			}
		} finally {
			this.loaded("application:user:whoAmI");
		}
	};

	updateUserProfile = async (values) => {
		this.loading("updateUserProfile");

		try {
			const path = `${trim(this.updateUserProfileUrl.replace(/\//g, "."), ".")}.${this.userProfile.id}`;

			const newUserProfile = merge({}, this.userProfile, values);
			await this.context.localStorageService.update(path, newUserProfile);

			this.setUserProfile(newUserProfile);
		} finally {
			this.loaded("updateUserProfile");
		}
	};

	/**
	 * Performs fetch on an external resource and checks status.
	 * @param url
	 * @param opts
	 * @returns {Promise<Response>}
	 */
	fetchExternalResource = async (url, opts = {}) => {
		if (isPlainObject(opts.body)) {
			if (!opts.headers) {
				opts.headers = {};
			}

			if (!opts.headers["Content-Type"]) {
				opts.headers["Content-Type"] = "application/json;charset=utf-8";
			}

			opts.body = JSON.stringify(opts.body);
		}

		opts.mode = 'cors';

		const result = await fetch(url, opts);

		if (result.ok) {
			return result;
		} else {
			throw new HTTPError(result);
		}
	};

	/**
	 * Performs fetch on local resource.
	 * @param url
	 * @param opts
	 * @returns {Promise<Response>}
	 */
	fetchResource = (url, opts = {}) => {
		throw new ApplicationError("errors.unsupportedMethod");
	};

	/**
	 * Performs authenticated fetch on local resource.
	 * @param url
	 * @param opts
	 * @returns {Promise<Response>}
	 */
	fetchProtectedResource = async (url, opts = {}) => {
		throw new ApplicationError("errors.unsupportedMethod");
	};
}
