import {action, observable, toJS} from "mobx";
import BaseService from "./BaseService";
import {isNumeric} from "../utilities";
import {isArray, isEmpty} from "lodash";

/**
 * Generic service capable of handling all CRUD operations. Extend if needed.
 */
export default class CrudService extends BaseService {

	/**
	 * Url of the resource this services works with.
	 */
	resourceUrl;

	/**
	 * Name of the field that serves as a primary key, 99% of the cases this will be "id".
	 */
	primaryKeyField;

	/**
	 * All items loaded in the current app screen. ID => record.
	 * Used to avoid having duplicate items.
	 * @type {{}}
	 */
	@observable
	allLoadedItems = {};

	/**
	 * How many times is every item used in any of the collections of the service.
	 * When this count reaches zero, the item will be removed from the collection with all items.
	 * ID => Set of namespaces
	 * @type {{}}
	 */
	@observable
	allLoadedItemsCollectionsCount = {};

	/**
	 * Single items, to be used in forms (create, edit), display, etc.
	 * @type {{}}
	 */
	@observable
	item = {};

	/**
	 * Multiple ORDERED items. To be used as a page in a list.
	 * @type {{}}
	 */
	@observable
	itemList = {};

	/**
	 * Multiple UNORDERED items, with IDs as keys. To be used to fill out relation data.
	 * @type {{}}
	 */
	@observable
	itemMap = {};

	/**
	 * Pagination data.
	 * @type {{}}
	 */
	@observable
	pagination = {};

	/**
	 * Sorting data.
	 * @type {{}}
	 */
	@observable
	sorter = {};

	/**
	 * Filtering data.
	 * @type {{}}
	 */
	@observable
	filters = {};

	/**
	 * Search data.
	 * @type {{}}
	 */
	@observable
	search = {};

	/**
	 * Initialization.
	 * @param resourceUrl
	 * @param primaryKeyField name of the field containing the ID of each record.
	 */
	constructor({resourceUrl, primaryKeyField = "id"}) {
		super();

		this.resourceUrl = resourceUrl;
		this.primaryKeyField = primaryKeyField;
	}

	@action
	setItem = (newItem, namespace) => {
		const oldItem = this.item[namespace];
		if (!isEmpty(oldItem) && !!oldItem[this.primaryKeyField]) {
			this.removeFromItemPool(oldItem, namespace);
		}

		if (!isEmpty(newItem) && !!newItem[this.primaryKeyField]) {
			this.addToItemPool(newItem, namespace);
		}

		this.item[namespace] = newItem;
	};

	@action
	setItemList = (newItems, namespace) => {
		if (!isEmpty(this.itemList[namespace])) {
			this.itemList[namespace].filter(item => !isEmpty(item) && !!item[this.primaryKeyField]).forEach(item => this.removeFromItemPool(item, namespace));
		}

		newItems.filter(item => !isEmpty(item) && !!item[this.primaryKeyField]).forEach(item => this.addToItemPool(item, namespace));
		this.itemList[namespace] = newItems;
	};

	@action
	setItemMap = (newItems, namespace) => {
		if (!isEmpty(this.itemMap[namespace])) {
			Object.values(this.itemMap[namespace]).filter(item => !isEmpty(item) && !!item[this.primaryKeyField]).forEach(item => this.removeFromItemPool(item, namespace));
		}

		if (!this.itemMap[namespace]) {
			this.itemMap[namespace] = {};
		}

		newItems.forEach(item => {
			const primaryKey = item[this.primaryKeyField];

			if (!isEmpty(item) && !!item[this.primaryKeyField]) {
				this.addToItemPool(item, namespace);
			}
			this.itemMap[namespace][primaryKey] = item;
		});
	};

	/**
	 * Add item to the pool of all items.
	 * @param newItem
	 * @param namespace
	 */
	@action
	addToItemPool = (newItem, namespace) => {
		const primaryKey = newItem[this.primaryKeyField];
		let usageCnt = this.allLoadedItemsCollectionsCount[primaryKey];

		if (!usageCnt) {
			usageCnt = new Set();
			this.allLoadedItemsCollectionsCount[primaryKey] = usageCnt;
		}

		this.allLoadedItems[primaryKey] = newItem;
		usageCnt.add(namespace);
	};

	/**
	 * Remove item from the pool of all items.
	 * @param item
	 * @param namespace
	 */
	@action
	removeFromItemPool = (item, namespace) => {
		const primaryKey = item[this.primaryKeyField];
		let usageCnt = this.allLoadedItemsCollectionsCount[primaryKey];

		if (!!usageCnt && usageCnt.size > 0) {
			usageCnt.delete(namespace);
		}

		if (!!usageCnt && usageCnt.size < 1) {
			delete this.allLoadedItemsCollectionsCount[primaryKey];
		}

		if (!usageCnt || usageCnt.size < 1) {
			delete this.allLoadedItems[primaryKey];
		}
	};

	@action
	setPagination = (pagination, namespace) => {
		this.pagination[namespace] = pagination;
	};

	@action
	setSorter = (sorter, namespace) => {
		this.sorter[namespace] = sorter;
	};

	@action
	setFilters = (filters, namespace) => {
		this.filters[namespace] = filters;
	};

	@action
	removeFilter = (key, namespace) => {
		const {[key]: omit, ...rest} = this.filters[namespace];

		this.filters[namespace] = rest;
	};

	@action
	changeFilter = (key, filter, namespace) => {
		if (!this.filters[namespace]) {
			this.filters[namespace] = {};
		}
		this.filters[namespace][key] = filter;
	};

	@action
	setSearch = (search, namespace) => {
		this.search[namespace] = search;
	};

	/**
	 * Check if item exists by passed id.
	 * @param id
	 * @param namespace is the namespace of the component requesting this item.
	 */
	existsById = async (id, namespace) => {
		this.loading(`${namespace}:exists:${id}`);

		try {
			const url = `${this.resourceUrl}/exists/${id}`;

			const response = await this.context.appService.fetchProtectedResource(url);
			return JSON.parse(await response.text());
		} finally {
			this.loaded(`${namespace}:exists:${id}`);
		}
	};

	/**
	 * Check how many records are there currently.
	 * @param namespace is the namespace of the component requesting this item.
	 */
	count = async (namespace) => {
		this.loading(`${namespace}:count`);

		try {
			const url = `${this.resourceUrl}/count`;

			const response = await this.context.appService.fetchProtectedResource(url);
			return JSON.parse(await response.text());
		} finally {
			this.loaded(`${namespace}:count`);
		}
	};

	/**
	 * Load single item by passed id.
	 * @param id
	 * @param namespace is the namespace of the component requesting this item.
	 * @returns {Promise<void>}
	 */
	fetchItem = async (id, namespace) => {
		this.loading(`${namespace}:fetch:${id}`);

		try {
			const url = `${this.resourceUrl}/${id}`;

			const response = await this.context.appService.fetchProtectedResource(url);
			const item = await response.json();

			this.setItem(item, namespace);
		} finally {
			this.loaded(`${namespace}:fetch:${id}`);
		}
	};

	/**
	 * Load list of items. Takes into account sorter, filter, search and pagination.
	 * @returns {Promise<void>}
	 */
	fetchItemList = async (namespace) => {
		this.loading(`${namespace}:fetch:list`);

		try {
			const url = [`${this.resourceUrl}?`];

			this.appendSortAndOrderToUrl(url, this.sorter[namespace].field, this.sorter[namespace].order);
			this.appendFiltersToUrl(url, this.filters[namespace]);
			this.appendSearchToUrl(url, this.search[namespace]);
			this.appendPaginationToUrl(url, this.pagination[namespace]);

			const response = await this.context.appService.fetchProtectedResource(url.join(""));
			const pagedResults = await response.json();

			this.setPagination({...this.pagination[namespace], total: pagedResults.totalElements}, namespace);
			this.setItemList(pagedResults.content, namespace);
		} finally {
			this.loaded(`${namespace}:fetch:list`);
		}
	};

	/**
	 * Load map of items from a list of ids.
	 * @returns {Promise<void>}
	 */
	fetchItemMap = async (ids, namespace) => {
		this.loading(`${namespace}:fetch:map:${ids.join(",")}`);

		try {
			const url = [`${this.resourceUrl}?`];

			this.appendIdsToUrl(url, ids);

			const response = await this.context.appService.fetchProtectedResource(url.join(""));
			const results = await response.json();

			this.setItemMap(results, namespace);
		} finally {
			this.loaded(`${namespace}:fetch:map:${ids.join(",")}`);
		}
	};

	/**
	 * Save (create or update) a single item.
	 * @param item
	 * @param namespace
	 * @returns {Promise<void>}
	 */
	save = async (item, namespace) => {
		const id = item[this.primaryKeyField] || "";

		this.loading(`${namespace}:save:${id}`);

		try {
			const url = `${this.resourceUrl}/${id}`;

			const response = await this.context.appService.fetchProtectedResource(url, {
				method: !!id ? "PUT" : "POST",
				body: toJS(item)
			});
			return (await response.json())[this.primaryKeyField];
		} finally {
			this.loaded(`${namespace}:save:${id}`);
		}
	};

	/**
	 * Delete a single item.
	 * @param id
	 * @param namespace
	 * @returns {Promise<void>}
	 */
	delete = async (id, namespace) => {
		this.loading(`${namespace}:delete:${id}`);

		try {
			const url = `${this.resourceUrl}/${id}`;

			await this.context.appService.fetchProtectedResource(url, {
				method: "DELETE"
			});
		} finally {
			this.loaded(`${namespace}:delete:${id}`);
		}
	};

	/**
	 * Adds sorting and ordering parts to a passed URL.
	 * @param url
	 * @param field
	 * @param order
	 */
	appendSortAndOrderToUrl = (url, field, order) => {
		if (field && order) {
			url.push(`sort=${field},${order === "ascend" ? "asc" : "desc"}&`);
		}
	};

	/**
	 * Adds filtering parts to a passed URL.
	 * @param url
	 * @param filters
	 */
	appendFiltersToUrl = (url, filters) => {
		const filterQueryString = [];

		Object.entries(filters).forEach(([key, filter]) => {
			switch (filter.type) {
				case ">x<":
					filterQueryString.push(`${filter.field}>${filter.values[0]}`);
					filterQueryString.push(`${filter.field}<${filter.values[1]}`);
					break;
				case "=":
					filterQueryString.push(`${filter.field}:${filter.values[0]}`);
					break;
				default:
					console.log(`Unknown filter type: ${filter.type}`);
					break;
			}
		});

		if (filterQueryString.length > 0) {
			url.push(`filter=${filterQueryString.join(",")}&`);
		}
	};

	/**
	 * Adds searching parts to a passed URL.
	 * @param url
	 * @param search
	 */
	appendSearchToUrl = (url, search) => {
		if (isArray(search.fields)) {
			url.push(`searchFields=${search.fields.join(",")}&`);
			url.push(`searchValue=${search.value}&`);
		}
	};

	/**
	 * Adds pagination to a passed URL.
	 * @param url
	 * @param pagination
	 */
	appendPaginationToUrl = (url, pagination) => {
		const currentPage = pagination.current;
		const pageSize = pagination.pageSize;

		if (isNumeric(currentPage) && isNumeric(pageSize)) {
			url.push(`page=${currentPage - 1}&size=${pageSize}&`);
		}
	};

	/**
	 * Adds list of ids to passed URL.
	 * @param url
	 * @param ids
	 */
	appendIdsToUrl = (url, ids) => {
		if (!!ids && isArray(ids)) {
			url.push(`ids=${ids.join(",")}&`);
		}
	};

	/**
	 * Resets all records' data (e.g. item, items) in a namespace in this instance of the service.
	 * Useful when different components use different items of the same resource.
	 */
	resetNamespaceData = (namespace) => {
		this.setItem({}, namespace);
		this.setItemList([], namespace);
		this.setItemMap([], namespace);
	};

	/**
	 * Initializes a new namespace.
	 */
	createNamespace = (namespace) => {
		if (!this.pagination[namespace]) {
			this.setPagination({current: 1, pageSize: 20}, namespace);
		}

		if (!this.sorter[namespace]) {
			this.setSorter({}, namespace);
		}

		if (!this.filters[namespace]) {
			this.setFilters({}, namespace);
		}

		if (!this.search[namespace]) {
			this.setSearch({}, namespace);
		}
	}
}
