import DefaultApplicationContext from "../base/services/DefaultApplicationContext";
import InvoiceService from "./InvoiceService";
import CounterpartyService from "./CounterpartyService";
import InvoiceLineService from "./InvoiceLineService";
import DefaultLocalStorageService from "../base/services/DefaultLocalStorageService";
import CompanyService from "./CompanyService";
import LocalStorageApplicationService from "../base/services/LocalStorageApplicationService";
import DefaultUiService from "../base/services/DefaultUiService";
import UserService from "./UserService";
import EquipmentRepairService from "./EquipmentRepairService";
import {defaultCurrency, defaultLocale} from "../localizations";

export default class ApplicationContext extends DefaultApplicationContext {
	constructor() {
		super([
			new DefaultLocalStorageService(),
			new LocalStorageApplicationService({
				apiBaseUrl: process.env.API_URL,
				appClientId: "DigidWorksLime",
				defaultLocale: defaultLocale,
				defaultCurrency: defaultCurrency,
			}),
			new DefaultUiService(),
			new InvoiceService(),
			new CounterpartyService(),
			new InvoiceLineService(),
			new CompanyService(),
			new EquipmentRepairService(),
			new UserService(),
		]);
	}
}
