import React, {Component} from "react";
import PropTypes from 'prop-types';
import {DatePicker, Input, InputNumber, Select, Switch} from "antd";
import {Field} from "formik";
import {FlexCol, FlexRow} from "../layouts/FlexGrid";
import {FormattedMessage} from "react-intl";
import classNames from "classnames";
import moment from "moment";

class FormItem extends Component {

	static propTypes = {
		formControl: PropTypes.oneOfType([PropTypes.node, PropTypes.func]).isRequired,
		label: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
		id: PropTypes.string.isRequired,
		addonBefore: PropTypes.node,
		addonAfter: PropTypes.node
	};

	render() {
		const {label, id, formControl: Cmp, addonBefore, addonAfter, ...rest} = this.props;

		return <Field name={id}>{({field, form, meta}) => {
			const errors = form.errors[field.name];
			const touched = form.touched[field.name];
			const showErrors = !!errors && (!!touched || form.submitCount > 0);

			return <FlexRow>
				<FlexCol className="mb-3">
					<FlexRow className={classNames({
						"has-error": showErrors
					})}>
						{label && <FlexCol xs={24}>
							<label className="label" htmlFor={id}>{label}</label>
						</FlexCol>}
						<FlexCol xs={24}>
							{addonBefore || addonAfter ?
								<Input.Group compact>
									{addonBefore &&
									<div className="ant-input-group-addon">
										{addonBefore}
									</div>
									}
									<Cmp id={id} {...field} form={form} {...rest} />
									{addonAfter &&
									<div className="ant-input-group-addon">
										{addonAfter}
									</div>
									}
								</Input.Group> :
								<Cmp id={id} {...field} form={form} meta={meta} {...rest}/>
							}
						</FlexCol>
					</FlexRow>
					<FlexRow className={classNames({
						"has-error has-feedback": showErrors
					})}>
						{showErrors && <FlexCol xs={24}>
							<span className="ant-form-explain"><FormattedMessage id={errors}/></span>
						</FlexCol>}
					</FlexRow>
				</FlexCol>
			</FlexRow>;
		}}</Field>;
	}
}

export const FormItemInput = (props) => {
	return <FormItem formControl={({form, ...rest}) => {
		return <Input {...rest} />;
	}} {...props} />;
};

export const FormItemInputNumber = (props) => {
	return <FormItem formControl={({onChange, form, ...rest}) => {
		return <InputNumber
			{...rest}
			onChange={(value) => {
				form.setFieldValue(props.id, value);
			}}
		/>;
	}} {...props} />;
};

export const FormItemDatePicker = (props) => {
	const datePickerRef = React.createRef();
	return <FormItem formControl={({onChange, form, onBlur, value, ...rest}) => {
		return <DatePicker
			{...rest}
			ref={datePickerRef}
			onBlur={(evt) => {
				if (!datePickerRef.current.picker.state.open) {
					evt.target.name = props.id;
					evt.target.id = props.id;
					onBlur(evt);
				}
			}}
			onChange={async (value) => {
				await form.setFieldValue(props.id, value ? value.toISOString() : value);
				datePickerRef.current.blur();
			}}
			value={value ? moment(value) : undefined}
		/>;
	}} {...props} />;
};

export const FormItemSelect = (props) => {
	return <FormItem formControl={({onChange, onBlur, form, ...rest}) => {
		return <Select
			{...rest}
			onBlur={() => {
				form.setFieldTouched(props.id, true);
			}}
			onChange={(value) => {
				form.setFieldValue(props.id, value);
			}}
		/>;
	}} {...props} />;
};

export const FormItemSwitch = (props) => {
	return <FormItem formControl={({onChange, value, form, ...rest}) => {
		return <Switch {...rest}
					   checked={!!value}
					   onChange={(value) => {
						   form.setFieldValue(props.id, value);
					   }}
					   value={!!value}
		/>
	}} {...props} />;
};

export const FormItemClear = ({clearedValue, ...props}) => {
	return <FormItem formControl={({onChange, checked, value, form, meta, ...rest}) => {
		return <Switch {...rest}
					   defaultChecked={false}
					   onChange={(value) => {
						   if (value) {
							   form.setFieldValue(props.id, clearedValue);
						   } else {
							   form.setFieldValue(props.id, meta.initialValue);
						   }
					   }}
		/>;
	}} {...props} />;
};
