import React, {Component} from "react";
import {Button, Modal} from "antd";
import {inject, observer} from "mobx-react";
import {FormattedMessage} from "react-intl";
import PropTypes from "prop-types";
import {concat, uniq} from "lodash";

export default
@inject("context")
@observer
class PageTutorial extends Component {

	static propTypes = {
		namespace: PropTypes.string.isRequired,
		titleId: PropTypes.string.isRequired,
		children: PropTypes.any.isRequired,
	};

	static defaultProps = {};

	componentDidMount() {
		this.props.context.uiService.setModal(this.props.namespace, this.props.titleId, {visible: true});
	}

	render() {
		const {namespace, context, titleId} = this.props;
		const {uiService, appService} = context;
		const userOptedOut = appService.userProfile.optedOutTutorials && appService.userProfile.optedOutTutorials.indexOf(`${titleId}`) > -1;
		const visible = uiService.modals[namespace] && uiService.modals[namespace][titleId] && uiService.modals[namespace][titleId].visible && !userOptedOut;

		return <Modal visible={visible}
					  title={<FormattedMessage id={titleId}/>}
					  onOk={this.handleOkClicked}
					  onCancel={this.handleOkClicked}
					  footer={[
						  <Button key="submit" type="primary" onClick={this.handleOkClicked}>
							  <FormattedMessage id="ok"/>
						  </Button>,
						  <Button key="optOut" type="danger" onClick={this.handleOptOutClicked}>
							  <FormattedMessage id="tutorials.buttons.optOut"/>
						  </Button>
					  ]}
		>
			{this.props.children}
		</Modal>;
	}

	handleOkClicked = () => {
		this.props.context.uiService.setModal(this.props.namespace, this.props.titleId, {visible: false});
	};

	handleOptOutClicked = () => {
		this.props.context.appService.updateUserProfile({
			optedOutTutorials: uniq(concat(this.props.context.appService.userProfile.optedOutTutorials, [`${this.props.titleId}`])),
		});
	};
};
