import {inject, observer} from "mobx-react";
import React, {Component} from "react";
import {invoices} from "../../configurations/navigation";
import {FormattedDate, FormattedMessage, injectIntl} from "react-intl";
import DefaultListPage from "../shared/crud/DefaultList";
import {defaultDateFormat} from "../../base/configurations/formats";
import {Button, Icon} from "antd";
import {Link, withRouter} from "react-router-dom";
import DefaultTable from "../shared/crud/DefaultTable";
import {autorun} from "mobx";
import RelationField from "../../base/components/data-display/RelationField";
import {paddedNumberFormatter} from "../../base/utilities";
import TableArrowExpandIcon from "../../base/components/data-display/TableArrowExpandIcon";
import InvoiceLineEmbeddedListPage from "../invoice-lines/EmbeddedListPage";
import {defaultErrorHandler} from "../../base/errors/defaultErrorHandler";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";
import TutorialTip from "../../base/components/data-display/TutorialTip";

export default
@inject("invoiceService", "counterpartyService")
@injectIntl
@withRouter
@observer
class InvoiceListPage extends Component {
	namespace = "invoiceListPage";

	constructor(props) {
		super(props);

		this.state = {
			counterpartyAutorunnerDisposer: null,
		};
	}

	componentWillUnmount() {
		const {counterpartyAutorunnerDisposer} = this.state;
		if (counterpartyAutorunnerDisposer) {
			counterpartyAutorunnerDisposer();
		}

		this.props.counterpartyService.resetNamespaceData(this.namespace);
	}

	componentDidMount() {
		const disposer = autorun(async reaction => {
			const namespace = this.namespace;
			const counterpartyService = this.props.counterpartyService;
			const service = this.props.invoiceService;

			const invoices = service.itemList[namespace];

			if (!invoices || invoices.length < 1) {
				return;
			}

			const counterpartyIds = new Set();

			invoices.forEach(invoice => counterpartyIds.add(invoice.counterparty_id));

			try {
				await counterpartyService.fetchItemMap(Array.from(counterpartyIds), namespace);
			} catch (e) {
				defaultErrorHandler(this.props, "application.error.get", e);
			}

		});

		this.setState({
			counterpartyAutorunnerDisposer: disposer
		});
	}

	render() {
		const namespace = this.namespace;
		const service = this.props.invoiceService;
		const counterpartyService = this.props.counterpartyService;
		const titleId = "components.invoices.list.title";
		const locationState = this.props.location.state;
		let defaultExpandedRowKeys = [];

		if (!!locationState) {
			defaultExpandedRowKeys.push(parseInt(locationState.params.invoiceId, 10));
		}

		const searchFields = [
			"number",
			"counterparty.company",
			"date",
			"totalPaymentAdditional",
		];
		const columns = [
			{
				title: () => <FormattedMessage id="components.invoices.number"/>,
				dataIndex: "number",
				sorter: true,
				render: (value, record) => paddedNumberFormatter(value)
			},
			{
				title: () => <FormattedMessage id="components.invoices.date"/>,
				dataIndex: "date",
				sorter: true,
				render: (value, record) => <FormattedDate value={new Date(value)} {...defaultDateFormat}/>
			},
			{
				title: () => <FormattedMessage id="components.invoices.counterparty"/>,
				dataIndex: "counterparty.company",
				sorter: true,
				render: (value, record) => <RelationField crudService={counterpartyService} namespace={namespace} itemKey={record.counterparty_id} displayProp={"company"}/>
			},
			{
				title: () => <FormattedMessage id="components.invoices.totalPaymentAdditional"/>,
				dataIndex: "totalPaymentAdditional",
				sorter: true,
			},
		];

		const additionalActions = [
			(props) => (<TutorialTip title={<FormattedMessage id="tooltips.invoices.list.invoicePreviewButton"/>}>
				<Button>
					<Link to={`${invoices.href}/${props.record.id}/preview`}>
						<Icon type="printer"/>
					</Link>
				</Button>
			</TutorialTip>)
		];

		return <DefaultListPage
			titleId={titleId}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.invoices.list">
				<FormattedMessage id="tutorials.texts.invoices.list" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.listPage" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultTable
				namespace={namespace}
				navConfig={invoices}
				crudService={service}
				columns={columns}
				searchFields={searchFields}
				additionalActions={additionalActions}
				tableProps={{
					rowKey: "id",
					expandRowByClick: true,
					expandIcon: TableArrowExpandIcon,
					defaultExpandedRowKeys,
					expandedRowRender: (record) => {
						return <InvoiceLineEmbeddedListPage invoiceId={record.id}/>;
					}
				}}
			/>
		</DefaultListPage>;
	}
}
