import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import writtenNumber from "written-number";
import DefaultLoadingPage from "../../base/components/data-display/DefaultLoadingPage";
import {intToCurrencyFormatter, paddedNumberFormatter} from "../../base/utilities";
import moment from "moment";
import {FormattedMessage, FormattedNumber, injectIntl} from "react-intl";
import {defaultErrorHandler} from "../../base/errors/defaultErrorHandler";
import {Button, Card, Icon, Radio, Typography} from "antd";
import {dashboard, invoices} from "../../configurations/navigation";
import DefaultBreadcrumbs from "../../base/components/layouts/DefaultBreadcrumbs";
import printJS from "print-js";
import {FlexCol, FlexRow} from "../../base/components/layouts/FlexGrid";
import Decimal from "decimal.js";
import {Helmet} from "react-helmet-async";
import TutorialTip from "../../base/components/data-display/TutorialTip";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

const invoiceStylesheets = [
	"/css/invoice-preview.css"
];

export default
@inject("context", "invoiceService", "invoiceLineService", "counterpartyService", "companyService")
@withRouter
@injectIntl
@observer
class InvoicePreviewPage extends Component {
	namespace = "invoicePreviewPage";

	constructor(props) {
		super(props);

		this.state = {
			type: "invoice",
		};
	}

	async componentDidMount() {
		const namespace = this.namespace;
		const invoiceId = parseInt(this.props.match.params.invoiceId, 10);
		const {invoiceService, invoiceLineService, counterpartyService, companyService} = this.props;

		invoiceLineService.createNamespace(namespace);
		companyService.createNamespace(namespace);

		try {
			invoiceLineService.changeFilter("invoice_id", {
				type: "=",
				field: "invoice_id",
				values: [invoiceId]
			}, namespace);

			const loadCompany = async () => {
				const hasCompany = await companyService.existsById(1, namespace);

				if (hasCompany) {
					await companyService.fetchItem(1, namespace);
				}
			};

			const loadInvoice = async () => {
				await invoiceService.fetchItem(invoiceId, namespace);
				await counterpartyService.fetchItem(invoiceService.item[namespace].counterparty_id, namespace);
			};

			await Promise.all([loadCompany(), loadInvoice(), invoiceLineService.fetchItemList(namespace)]);
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	}

	componentWillUnmount() {
		this.props.invoiceService.resetNamespaceData(this.namespace);
		this.props.invoiceLineService.resetNamespaceData(this.namespace);
		this.props.counterpartyService.resetNamespaceData(this.namespace);
		this.props.companyService.resetNamespaceData(this.namespace);
	}

	render() {
		const namespace = this.namespace;
		const {context, invoiceService, invoiceLineService, counterpartyService, companyService} = this.props;
		const {type} = this.state;

		const invoice = invoiceService.item[namespace] || {};
		const counterparty = counterpartyService.item[namespace] || {};
		const invoiceLines = invoiceLineService.itemList[namespace] || [];
		const company = companyService.item[namespace] || {};

		const currency = context.appService.settings.currency;

		if (invoiceService.isLoadingNamespace(namespace) ||
			invoiceLineService.isLoadingNamespace(namespace) ||
			counterpartyService.isLoadingNamespace(namespace) ||
			companyService.isLoadingNamespace(namespace)
		) {
			return <DefaultLoadingPage/>;
		}

		const invoiceTotal = Math.floor(intToCurrencyFormatter(invoiceLines.reduce((acc, line) => acc + line.unitPrice * line.quantity, 0)));
		const invoiceTotalEn = writtenNumber(invoiceTotal, {lang: "en"});

		let invoiceVatAccrued = 0;

		if (invoiceTotal) {
			invoiceVatAccrued = Decimal.mul(invoiceTotal, company.vatPercentage).div(100).toFixed(2);
		}

		const title = this.props.intl.formatMessage({id: "components.invoices.preview.title"}, {number: invoice.number});

		return <>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.invoices.preview">
				<FormattedMessage id="tutorials.texts.invoices.preview" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<Helmet>
				<title>{title} - {this.props.intl.formatMessage({id: "application.meta.title"})}</title>
				{invoiceStylesheets.map((stylesheet, index) => <link key={index} rel="stylesheet" href={stylesheet} type="text/css"/>)}
				<link rel="stylesheet" href={"https://fonts.googleapis.com/css?family=IBM+Plex+Sans|Montserrat:400,400i,500,500i,700,700i&display=swap&subset=cyrillic"}
					  type="text/css"/>
			</Helmet>
			<DefaultBreadcrumbs crumbs={[dashboard, invoices, {title}]}/>
			<Card id="invoice-controls" title={title} bordered={false}>
				<FlexRow>
					<FlexCol xs="auto">
						<Button block onClick={this.handleBackButtonClick}>
							<Icon type="left"/>
							<Typography.Text><FormattedMessage id="back"/></Typography.Text>
						</Button>
					</FlexCol>
					<FlexCol xs="auto" className="mx-auto">
						<TutorialTip title={<FormattedMessage id="tooltips.invoices.preview.invoiceTypeSelection"/>}>
							<Radio.Group defaultValue={type} buttonStyle="solid" onChange={this.handleTypeChange}>
								<Radio.Button value="invoice">
									<FormattedMessage id="components.invoices.preview.typeSwitch.invoice"/>
								</Radio.Button>
								<Radio.Button value="proforma">
									<FormattedMessage id="components.invoices.preview.typeSwitch.proforma"/>
								</Radio.Button>
							</Radio.Group>
						</TutorialTip>
					</FlexCol>
					<FlexCol xs="auto">
						<TutorialTip title={<FormattedMessage id="tooltips.invoices.preview.invoicePrintButton"/>}>
						<Button block type="primary" icon="printer" onClick={this.handlePrintClick}><FormattedMessage id="print" tagName="span"/></Button>
						</TutorialTip>
					</FlexCol>
				</FlexRow>
			</Card>
			<Card id="invoice" bordered={false}>
				<FlexRow>
					<FlexCol className="d-flex align-items-start">
						<img style={{maxWidth: "200px"}} src={company.logo} alt={company.name} title={company.name}/>
					</FlexCol>
					<FlexCol className="text-right">
						<h1 id="brand">{company.name}</h1>
					</FlexCol>
				</FlexRow>
				<FlexRow>
					<FlexCol className="text-center">
						<h2><FormattedMessage id="components.invoices.preview.header.ivnoice"/></h2>
						{type === "invoice" ? <h4><FormattedMessage id="components.invoices.preview.header.original"/></h4> :
							<h4><FormattedMessage id="components.invoices.preview.header.proforma"/></h4>}
						<h3 id="invoice-number"># {paddedNumberFormatter(invoice.number)}</h3>
						<h3>{moment(invoice.date).format("DD.MM.YYYY")}</h3>
						<h3>{company.city}</h3>
					</FlexCol>
				</FlexRow>
				<FlexRow className="invoice-section">
					<FlexCol sm={9} offsetSm={2}>
						<FlexRow><FlexCol className="company-header"><FormattedMessage id="components.invoices.preview.recipient" tagName="strong"/></FlexCol></FlexRow>
						<table>
							<tbody>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.company"/>:</td>
								<td><strong>{counterparty.company}</strong></td>
							</tr>

							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.address"/>:</td>
								<td><strong>{!counterparty.address ? "" : counterparty.address.split(", ").map((item, index) => <p key={index}
																																   style={{margin: 0}}>{item}</p>)}</strong></td>
							</tr>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.accountablePerson"/>:</td>
								<td><strong>{counterparty.accountablePerson}</strong></td>
							</tr>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.uic"/>:</td>
								<td><strong>{counterparty.uic}</strong></td>
							</tr>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.vatNumber"/>:</td>
								<td><strong>{counterparty.vatNumber || ""}</strong></td>
							</tr>
							</tbody>
						</table>
					</FlexCol>
					<FlexCol sm={9} offsetSm={2}>
						<FlexRow><FlexCol className="company-header"><FormattedMessage id="components.invoices.preview.provider" tagName="strong"/></FlexCol></FlexRow>
						<table>
							<tbody>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.company"/>:</td>
								<td><strong>{company.name}</strong></td>
							</tr>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.address"/>:</td>
								<td><strong>{company.address}</strong></td>
							</tr>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.accountablePerson"/>:</td>
								<td><strong>{company.accountablePerson}</strong></td>
							</tr>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.uic"/>:</td>
								<td><strong>{company.uic}</strong></td>
							</tr>
							<tr>
								<td className="company-info-row"><FormattedMessage id="components.invoices.preview.vatNumber"/>:</td>
								<td><strong>{company.vatNumber}</strong></td>
							</tr>
							</tbody>
						</table>
					</FlexCol>
				</FlexRow>
				<FlexRow className="invoice-section">
					<FlexCol>
						<table>
							<thead>
							<tr>
								<th>#</th>
								<th>
									<div><FormattedMessage id="components.invoices.preview.goodsName"/></div>
								</th>
								<th>
									<div><FormattedMessage id="components.invoices.preview.measure"/></div>
								</th>
								<th>
									<div><FormattedMessage id="components.invoices.preview.quantity"/></div>
								</th>
								<th>
									<div><FormattedMessage id="components.invoices.preview.unitPrice"/> ({currency})</div>
								</th>
								<th>
									<div><FormattedMessage id="components.invoices.preview.price"/> ({currency})</div>
								</th>
							</tr>
							</thead>
							<tbody>
							{invoiceLines.map((line, index) => {
								return <tr key={line.id}>
									<th scope="row">{index + 1}</th>
									<td>{line.description}</td>
									<td>{line.measure}</td>
									<td>{line.quantity}</td>
									<td>
										<FormattedNumber value={intToCurrencyFormatter(line.unitPrice)} format={currency}/>
									</td>
									<td>
										<FormattedNumber value={intToCurrencyFormatter(line.unitPrice * line.quantity)} format={currency}/>
									</td>
								</tr>;
							})}
							</tbody>
						</table>
					</FlexCol>
				</FlexRow>
				<FlexRow className="invoice-section">
					<FlexCol xs={10}>
						<FlexRow>
							<FlexCol xs="auto" className="details-box-title">
								<div className="smaller-heading"><FormattedMessage id="components.invoices.preview.paymentMethod"/>:</div>
							</FlexCol>
							<FlexCol>
								<div className="smaller-heading"><FormattedMessage id="components.invoices.preview.bankTransaction"/></div>
							</FlexCol>
						</FlexRow>
						<FlexRow>
							<FlexCol offsetXs={2} className="details-box-table">
								<table>
									<tbody>
									<tr>
										<td>
											<div><FormattedMessage id="components.invoices.preview.bank"/>:</div>
										</td>
										<td className="text-right">
											<div>{company.bankName}</div>
										</td>
									</tr>
									<tr>
										<td>BIC:</td>
										<td className="text-right">{company.bankBic}</td>
									</tr>
									<tr>
										<td>IBAN:</td>
										<td className="text-right">{company.bankIban}</td>
									</tr>
									</tbody>
								</table>
							</FlexCol>
						</FlexRow>
						<FlexRow style={{marginBottom: "10px"}}>
							<FlexCol xs="auto" className="details-box-title">
								<h4>
									<div><FormattedMessage id="components.invoices.preview.paymentTerms"/>:</div>
								</h4>
							</FlexCol>
							<FlexCol>
								<h4>
									<div><FormattedMessage id="components.invoices.preview.paymentTermsDays"/></div>
								</h4>
							</FlexCol>
						</FlexRow>
						<FlexRow>
							<FlexCol xs="auto" className="details-box-title">
								<div><FormattedMessage id="components.invoices.preview.amountInWords"/>:</div>
							</FlexCol>
							<FlexCol>
								<div>{invoiceTotalEn} {currency}</div>
							</FlexCol>
						</FlexRow>
					</FlexCol>
					<FlexCol xs={10} offsetXs={2}>
						<FlexRow>
							<FlexCol className="details-box-title">
								<div className="smaller-heading"><FormattedMessage id="components.invoices.preview.totalDue"/></div>
							</FlexCol>
						</FlexRow>
						<FlexRow>
							<FlexCol offsetXs={1} className="details-box-table">
								<table>
									<tbody>
									<tr>
										<td>
											<div><FormattedMessage id="components.invoices.preview.subtotalBaseVat"/></div>
										</td>
										<td className="text-right"><FormattedNumber value={invoiceTotal} format="decimal"/> {currency}</td>
									</tr>
									<tr>
										<td>
											<div><FormattedMessage id="components.invoices.preview.vat"/></div>
										</td>
										<td className="text-right">{company.vatPercentage}%</td>
									</tr>
									<tr>
										<td>
											<div><FormattedMessage id="components.invoices.preview.vatAccrued"/></div>
										</td>
										<td className="text-right">{<FormattedNumber value={invoiceVatAccrued} format="decimal"/> || ""} {currency}</td>
									</tr>
									<tr>
										<td>
											<div><FormattedMessage id="components.invoices.preview.totalPayment"/></div>
										</td>
										<td className="text-right">{invoiceTotal ?
											<FormattedNumber value={Decimal.add(invoiceTotal, invoiceVatAccrued)}
															 format="decimal"/> : ""} {currency} {invoice.totalPaymentAdditional}</td>
									</tr>
									</tbody>
								</table>
							</FlexCol>
						</FlexRow>
					</FlexCol>
				</FlexRow>
				<FlexRow className="invoice-section">
					<FlexCol>
						<div className="text-center">
							<p className="signature"><FormattedMessage id="components.invoices.preview.receivedBy"/>: </p>
							<p>{counterparty.accountablePerson}</p>
							<p>{counterparty.accountablePersonTitle}</p>
						</div>
					</FlexCol>
					<FlexCol>
						<div className="text-center">
							<p className="signature"><FormattedMessage id="components.invoices.preview.invoicedBy"/>: </p>
							<p>{company.accountablePerson}</p>
							<p>{company.accountablePersonTitle}</p>
						</div>
					</FlexCol>
				</FlexRow>
			</Card>
		</>;
	}

	handleBackButtonClick = () => {
		// this.props.history.push(`${invoices.href}`, this.props.match);
		this.props.history.push(`${invoices.href}`);
	};

	handlePrintClick = () => {
		printJS({
			printable: "invoice",
			type: "html",
			maxWidth: null,
			scanStyles: false,
			documentTitle: `Invoice_${paddedNumberFormatter(this.props.invoiceService.item[this.namespace].number)}`,
			css: [...invoiceStylesheets, "https://fonts.googleapis.com/css?family=IBM+Plex+Sans|Montserrat:400,400i,500,500i,700,700i&display=swap&subset=cyrillic"],
			style: Array.from(document.querySelectorAll("head style")).map(el => el.innerHTML).join("\n")
		});
	};

	handleTypeChange = (e) => {
		this.setState({type: e.target.value});
	};


}
