import React, {Component} from "react";
import {Button, Icon, Input, message, Popconfirm, Table} from "antd";
import {FormattedMessage, injectIntl} from "react-intl";
import {debounce} from "lodash";
import PropTypes from "prop-types";
import CrudService from "../../../base/services/CrudService";
import {observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import {defaultErrorHandler} from "../../../base/errors/defaultErrorHandler";
import {FlexCol, FlexRow} from "../../../base/components/layouts/FlexGrid";

export default
@withRouter
@injectIntl
@observer
class DefaultTable extends Component {
	static propTypes = {
		crudService: PropTypes.instanceOf(CrudService).isRequired,
		navConfig: PropTypes.object.isRequired,
		columns: PropTypes.arrayOf(PropTypes.object).isRequired,
		searchFields: PropTypes.arrayOf(PropTypes.string).isRequired,
		namespace: PropTypes.string.isRequired,
		additionalActions: PropTypes.arrayOf(PropTypes.elementType).isRequired,
		tableProps: PropTypes.object,
	};

	static defaultProps = {
		additionalActions: [],
		tableProps: {},
	};

	constructor(props) {
		super(props);

		props.crudService.createNamespace(props.namespace);
	}

	componentWillUnmount() {
		this.props.crudService.resetNamespaceData(this.props.namespace);
	}

	componentDidMount() {
		this.reloadItems();
	}

	render() {
		const {crudService, navConfig, namespace, additionalActions, tableProps} = this.props;
		const sortData = crudService.sorter[namespace];

		const actionsColumn = {
			title: () => <FormattedMessage id="application.list.default.actions"/>,
			key: "actions",
			align: "right",
			render: (text, record) => {
				return <FlexRow>
					<FlexCol>
						<Button.Group className="justify-content-end" style={{display: "flex"}}>
							{additionalActions.length > 0 && additionalActions.map((ActionCmp, i) => <ActionCmp key={i} {...{text, record}}/>)}
							<Button onClick={() => this.props.history.push(`${navConfig.href}/${record.id}`)}>
								<Icon type="edit"/>
								<FormattedMessage id="edit" tagName="span"/>
							</Button>
							<Popconfirm title={<FormattedMessage id="application.list.default.confirmDelete"/>}
										okText={<FormattedMessage id="ok"/>}
										cancelText={<FormattedMessage id="cancel"/>}
										onConfirm={() => this.handleDeleteConfirmClick(record.id)}>
								<Button type="danger">
									<Icon type="delete"/>
									<FormattedMessage id="delete" tagName="span"/>
								</Button>
							</Popconfirm>
						</Button.Group>
					</FlexCol>
				</FlexRow>
			}
		};

		const columns = [...this.props.columns, actionsColumn];
		columns.forEach(col => {
			if (col.dataIndex && col.dataIndex === sortData.field) {
				col.sortOrder = sortData.order;
			}
		});

		return <>
			<FlexRow>
				<FlexCol>
					<Button onClick={() => this.props.history.push(`${navConfig.href}/create`)} type="primary">
						<FormattedMessage id="application.list.default.addNew" tagName="span"/>
					</Button>
				</FlexCol>
				<FlexCol>
					<Input.Search
						placeholder={this.props.intl.formatMessage({id: "application.omnisearch.default.placeholder"})}
						onSearch={this.handleSearchChange}
						onChange={this.handleSearchChange}
						value={crudService.search[namespace].value}
					/>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol>
					<Table style={{overflowX: "auto", marginTop: "10px"}}
						   rowKey="id"
						   columns={columns}
						   dataSource={crudService.itemList[namespace]}
						   pagination={{...crudService.pagination[namespace], position: "both"}}
						   loading={crudService.isLoading}
						   onChange={this.handleTableChange}
						   {...tableProps}
					/>
				</FlexCol>
			</FlexRow>
		</>;
	}

	handleTableChange = (pagination, filters, sorter) => {
		const crudService = this.props.crudService;
		const namespace = this.props.namespace;

		crudService.setPagination(pagination, namespace);
		crudService.setSorter(sorter, namespace);

		this.reloadItems();
	};

	handleSearchChange = (...args) => {
		const value = args.length === 2 ? args[1].currentTarget.value : args[0].currentTarget.value;

		this.props.crudService.setSearch({
			fields: this.props.searchFields,
			value
		}, this.props.namespace);

		this.debouncedReloadItems();
	};

	handleDeleteConfirmClick = async (id) => {
		const crudService = this.props.crudService;

		try {
			await crudService.delete(id, this.props.namespace);
			await this.reloadItems();
			message.success(this.props.intl.formatMessage({id: "application.success.delete"}));
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.delete", e);
		}
	};

	reloadItems = async () => {
		try {
			await this.props.crudService.fetchItemList(this.props.namespace);
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	};

	debouncedReloadItems = debounce(this.reloadItems, 250);
}
