import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import {equipmentRepairs} from "../../configurations/navigation";
import DefaultEditPage from "../shared/crud/DefaultEdit";
import DefaultForm from "../shared/crud/DefaultForm";
import EquipmentRepairFormFields, {equipmentRepairValidation} from "./FormFields";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

export default
@inject("equipmentRepairService")
@withRouter
@observer
class EquipmentRepairEditPage extends Component {
	render() {
		const namespace = "equipmentRepairEditPage";
		const itemId = parseInt(this.props.match.params.equipmentRepairId, 10);
		const {equipmentRepairService} = this.props;
		let record = equipmentRepairService.item[namespace];

		let initialValues = {};
		let titleParams = {};

		if (record) {
			initialValues = {
				description: record.description,
				date: record.date,
				cost: record.cost,
			};

			titleParams = {
				name: record.description
			};
		}

		return <DefaultEditPage
			crudService={equipmentRepairService}
			titleId="components.equipmentRepairs.edit.title"
			titleParams={titleParams}
			itemId={itemId}
			crumbs={[equipmentRepairs]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.equipmentRepairs.edit">
				<FormattedMessage id="tutorials.texts.equipmentRepairs.edit" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultForm
				crudService={equipmentRepairService}
				navConfig={equipmentRepairs}
				itemId={itemId}
				namespace={namespace}
				initialValues={initialValues}
				validationSchema={equipmentRepairValidation}>
				<EquipmentRepairFormFields/>
			</DefaultForm>
		</DefaultEditPage>;
	}
}
