import {isEmpty, isPlainObject, isString, merge} from "lodash";
import HTTPError from "../errors/HTTPError";
import BaseService from "./BaseService";
import {action, computed, observable, toJS} from "mobx";
import ApplicationError from "../errors/ApplicationError";

export default class DefaultApplicationService extends BaseService {
	/**
	 * Base url of the backend API this web application works with.
	 */
	apiBaseUrl;

	/**
	 * Client name for this web application.
	 */
	appClientId;

	/**
	 * Url to get oAuth tokens from.
	 * @type {string}
	 */
	oAuthTokenUrl = "/oauth/token";

	/**
	 * WHO AM I?!
	 * @type {string}
	 */
	whoAmIUrl = "/api/users/whoami";

	/**
	 * URL used to update the current user profile.
	 * @type {string}
	 */
	updateUserProfileUrl = "/api/users";

	/**
	 * Current PERSISTED app settings.
	 * @type {{}}
	 */
	@observable
	settings = {};

	/**
	 * OAuth data, most importantly access_token
	 * @type {{}}
	 */
	@observable
	oAuthData;

	/**
	 * Currently logged in user.
	 * @type {{}}
	 */
	@observable
	userProfile = {};

	/**
	 * Menu items in the sidebar of the application.
	 * @type {Array}
	 */
	@observable
	sidebarMenuItems = [];

	constructor({apiBaseUrl, appClientId, defaultLocale, defaultCurrency}) {
		super();

		this.apiBaseUrl = apiBaseUrl;
		this.appClientId = appClientId;
		this.settings.locale = defaultLocale;
		this.settings.currency = defaultCurrency;
	}

	/**
	 * @Override
	 */
	init = async () => {
		this.loading("application:initialization");

		try {
			const savedSettings = await this.context.localStorageService.load("settings");
			this.setSettings(savedSettings);

			const savedOAuthData = await this.context.localStorageService.load("oAuthData");

			try {
				if (isEmpty(savedOAuthData)) {
					return;
				}

				this.setOAuthData(savedOAuthData);
				await this.whoAmI();
			} catch (e) {
				if (!e.isUnauthorized) {
					throw e;
				}
			}
		} finally {
			this.loaded("application:initialization");
		}
	};

	/**
	 * Change a setting and save it to local storage.
	 * @param key
	 * @param value
	 */
	changeSetting = (key, value) => {
		this.setSetting(key, value);

		this.context.localStorageService.save("settings", toJS(this.settings));
	};

	setSettings = (settings) => {
		if (isPlainObject(settings) && !isEmpty(settings)) {
			Object.keys(settings).forEach(k => {
				this.setSetting(k, settings[k]);
			});
		}
	};

	@action
	setSetting = (k, v) => {
		this.settings[k] = v;
	};

	@action
	setUserProfile = (userProfile) => {
		this.userProfile = userProfile;
	};

	@action
	setOAuthData = (oAuthData) => {
		this.oAuthData = oAuthData;
	};

	@action
	setSidebarMenuItems = (sidebarMenuItems) => {
		this.sidebarMenuItems = sidebarMenuItems;
	};

	@computed
	get hasToken() {
		if (!this.oAuthData) {
			return false;
		}

		return Object.entries(this.oAuthData).length > 0;
	}

	@computed
	get hasUserProfile() {
		if (!this.userProfile) {
			return false;
		}

		return Object.entries(this.userProfile).length > 0;
	}

	@computed
	get isAuthenticated() {
		return this.hasToken && this.hasUserProfile;
	}

	/**
	 * Exchanges credentials for access_token.
	 * @param username
	 * @param password
	 * @returns {Promise<void>}
	 */
	login = async (username, password) => {
		this.loading("application:user:login");

		try {
			let body = new URLSearchParams();
			body.set("grant_type", "password");
			body.set("username", username);
			body.set("password", password);
			body.set("client_id", this.appClientId);

			const oAuthData = await (await this.fetchResource(this.oAuthTokenUrl, {
				method: "POST",
				body
			})).json();

			if (!isString(oAuthData.access_token) || oAuthData.access_token.length < 1) {
				throw new ApplicationError("errors.oauth.invalidResponse");
			}
			this.setOAuthData(oAuthData);
			this.whoAmI();
			this.context.localStorageService.save("oAuthData", toJS(this.oAuthData));
		} finally {
			this.loaded("application:user:login");
		}
	};

	/**
	 * Loads the user profile.
	 * @returns {Promise<void>}
	 */
	whoAmI = async () => {
		this.loading("application:user:whoAmI");

		try {
			const userProfile = await (await this.fetchProtectedResource(this.whoAmIUrl)).json();

			this.setUserProfile(userProfile);
		} finally {
			this.loaded("application:user:whoAmI");
		}
	};

	updateUserProfile = async (values) => {
		this.loading("updateUserProfile");

		try {
			const url = `${this.updateUserProfileUrl}/${this.userProfile.id}`;

			const response = await this.fetchProtectedResource(url, {
				method: "PUT",
				body: merge({}, this.userProfile, values)
			});

			const userProfile = await response.json();
			this.setUserProfile(userProfile);
		} finally {
			this.loaded("updateUserProfile");
		}
	};

	/**
	 * Destroys the current session.
	 */
	logout = () => {
		this.setOAuthData({});
		this.setUserProfile({});
		this.context.localStorageService.save("oAuthData", toJS(this.oAuthData));
	};

	/**
	 * Performs fetch on an external resource and checks status.
	 * @param url
	 * @param opts
	 * @returns {Promise<Response>}
	 */
	fetchExternalResource = async (url, opts = {}) => {

		if (isPlainObject(opts.body)) {
			if (!opts.headers) {
				opts.headers = {};
			}

			if (!opts.headers["Content-Type"]) {
				opts.headers["Content-Type"] = "application/json;charset=utf-8";
			}

			opts.body = JSON.stringify(opts.body);
		}

		opts.mode = 'cors';

		const result = await fetch(url, opts);

		if (result.ok) {
			return result;
		} else {
			throw new HTTPError(result);
		}
	};

	/**
	 * Performs fetch on local resource.
	 * @param url
	 * @param opts
	 * @returns {Promise<Response>}
	 */
	fetchResource = (url, opts = {}) => {
		return this.fetchExternalResource(`${this.apiBaseUrl}${url}`, opts);
	};

	/**
	 * Performs authenticated fetch on local resource.
	 * @param url
	 * @param opts
	 * @returns {Promise<Response>}
	 */
	fetchProtectedResource = async (url, opts = {}) => {
		if (!opts.headers) {
			opts.headers = {};
		}

		if (!opts.headers["Authorization"] && this.hasToken) {
			opts.headers["Authorization"] = `Bearer ${this.oAuthData.access_token}`;
		}

		try {
			return await this.fetchResource(url, opts);
		} catch (e) {
			if (e.isUnauthorized) {
				console.log("Unauthorized, probably token has expired, redirecting to login page...");
				this.logout();
				throw e;
			}
		}
	};
}
