import {computed} from "mobx";
import {camelCase} from "lodash";
import DefaultApplicationService from "./DefaultApplicationService";
import DefaultLocalStorageService from "./DefaultLocalStorageService";
import DefaultUiService from "./DefaultUiService";

/**
 * Default application context containing all shared global state. It enables different services to access one another.
 */
class DefaultApplicationContext {

	/**
	 * All application services reside here.
	 * @type {{}}
	 */
	services = {};

	/**
	 * Name of the service containing application data like API URLs, authentication tokens, etc.
	 */
	applicationServiceName;

	/**
	 * Name of the local storage service used to save data locally.
	 */
	localStorageServiceName;

	/**
	 * Name of the UI service used to manage data for UI components like modals.
	 */
	uiServiceName;

	constructor(services) {
		services.filter(s => !!s).forEach(service => {
			const name = camelCase(service.constructor.name);
			service.setContext(this);
			this.services[name] = service;

			if (service instanceof DefaultApplicationService) {
				this.applicationServiceName = camelCase(service.constructor.name);
			}

			if (service instanceof DefaultLocalStorageService) {
				this.localStorageServiceName = camelCase(service.constructor.name);
			}

			if (service instanceof DefaultUiService) {
				this.uiServiceName = camelCase(service.constructor.name);
			}
		});

		Object.values(this.services).forEach(service => {
			service.init();
		});
	}

	/**
	 * Shortcut to the Application Service.
	 */
	@computed
	get appService() {
		return this.services[this.applicationServiceName];
	}

	/**
	 * Shortcut to the Local Storage Service.
	 */
	@computed
	get localStorageService() {
		return this.services[this.localStorageServiceName];
	}

	/**
	 * Shortcut to the UI Service.
	 */
	@computed
	get uiService() {
		return this.services[this.uiServiceName];
	}
}

export default DefaultApplicationContext;
