import {inject, observer} from "mobx-react";
import React, {Component} from "react";
import {buildOneLevelNestedRelationNavConfig, invoiceLines} from "../../configurations/navigation";
import {FormattedMessage, FormattedNumber} from "react-intl";
import DefaultTable from "../shared/crud/DefaultTable";
import PropTypes from "prop-types";
import {intToCurrencyFormatter} from "../../base/utilities";

export default
@inject("context", "invoiceLineService")
@observer
class InvoiceLineEmbeddedListPage extends Component {
	static propTypes = {
		invoiceId: PropTypes.number.isRequired,
	};

	constructor(props) {
		super(props);

		const namespace = this.namespace;
		const invoiceId = this.props.invoiceId;
		const service = this.props.invoiceLineService;
		service.createNamespace(namespace);
		service.changeFilter("invoice_id", {
			type: "=",
			field: "invoice_id",
			values: [invoiceId]
		}, namespace);
	}

	render() {
		const namespace = this.namespace;
		const invoiceId = this.props.invoiceId;
		const service = this.props.invoiceLineService;
		const searchFields = [
			"description",
			"measure",
			"quantity",
			"unitPrice",
		];
		const currency = this.props.context.appService.settings.currency;
		const columns = [
			{
				title: () => <FormattedMessage id="components.invoiceLines.description"/>,
				dataIndex: "description",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.invoiceLines.measure"/>,
				dataIndex: "measure",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.invoiceLines.quantity"/>,
				dataIndex: "quantity",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.invoiceLines.unitPriceNoVat"/>,
				dataIndex: "unitPrice",
				sorter: true,
				render: (value, record) => <FormattedNumber value={intToCurrencyFormatter(value)} format={currency}/>
			},
		];

		return <DefaultTable
			namespace={namespace}
			navConfig={buildOneLevelNestedRelationNavConfig(invoiceLines, invoiceId)}
			crudService={service}
			columns={columns}
			searchFields={searchFields}
		/>;
	}

	get namespace() {
		return `invoiceLineEmbeddedListPage_${this.props.invoiceId}`;
	}
}
