import {message} from "antd";
import ApplicationError from "./ApplicationError";

export const defaultErrorHandler = ({intl}, id, error) => {
	console.log(error);

	if (error instanceof ApplicationError) {
		message.error(intl.formatMessage({id}, {e: intl.formatMessage({id: error.intlId})}));
		return;
	}
	message.error(intl.formatMessage({id}, {e: error.message}));
};
