import {inject} from "mobx-react";
import React, {Component} from "react";
import {counterparties} from "../../configurations/navigation";
import {FormattedMessage} from "react-intl";
import DefaultListPage from "../shared/crud/DefaultList";
import DefaultTable from "../shared/crud/DefaultTable";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

export default
@inject("counterpartyService")
class CounterpartyListPage extends Component {
	render() {
		const namespace = "counterpartyListPage";
		const service = this.props.counterpartyService;
		const titleId = "components.counterparties.list.title";
		const searchFields = [
			"company",
			"address",
			"accountablePerson",
			"accountablePersonTitle",
			"uic",
			"vatNumber",
		];
		const columns = [
			{
				title: () => <FormattedMessage id="components.counterparties.company"/>,
				dataIndex: "company",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.counterparties.address"/>,
				dataIndex: "address",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.counterparties.accountablePerson"/>,
				dataIndex: "accountablePerson",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.counterparties.accountablePersonTitle"/>,
				dataIndex: "accountablePersonTitle",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.counterparties.uic"/>,
				dataIndex: "uic",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.counterparties.vatNumber"/>,
				dataIndex: "vatNumber",
				sorter: true,
			},
		];

		return <DefaultListPage
			titleId={titleId}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.counterparties.list">
				<FormattedMessage id="tutorials.texts.counterparties.list" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.listPage" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultTable
				namespace={namespace}
				navConfig={counterparties}
				crudService={service}
				columns={columns}
				searchFields={searchFields}
			/>
		</DefaultListPage>;
	}
}
