import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import {invoices} from "../../configurations/navigation";
import DefaultEditPage from "../shared/crud/DefaultEdit";
import DefaultForm from "../shared/crud/DefaultForm";
import InvoiceLineFormFields, {invoiceLineValidation} from "./FormFields";
import {defaultErrorHandler} from "../../base/errors/defaultErrorHandler";
import {paddedNumberFormatter} from "../../base/utilities";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

export default @inject("invoiceLineService", "invoiceService")
@withRouter
@observer
class InvoiceLineEditPage extends Component {
	namespace = "invoiceLineEditPage";

	async componentDidMount() {
		const namespace = this.namespace;
		const invoiceId = parseInt(this.props.match.params.invoiceId, 10);
		const {invoiceService} = this.props;

		try {
			await invoiceService.fetchItem(invoiceId, namespace);
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	}

	render() {
		const namespace = this.namespace;
		const itemId = parseInt(this.props.match.params.invoiceLineId, 10);
		const invoiceId = parseInt(this.props.match.params.invoiceId, 10);
		const {invoiceLineService, invoiceService} = this.props;
		const record = invoiceLineService.item[namespace];
		const invoice = invoiceService.item[namespace];

		let initialValues = {};
		let titleParams = {};

		if (record) {
			initialValues = {
				description: record.description,
				measure: record.measure,
				quantity: record.quantity,
				unitPrice: record.unitPrice,
			};

			titleParams.name = record.description;
		}

		titleParams.invoice = invoice ? paddedNumberFormatter(invoice.number) : "";

		return <DefaultEditPage
			crudService={invoiceLineService}
			titleId="components.invoiceLines.edit.title"
			titleParams={titleParams}
			itemId={itemId}
			crumbs={[invoices]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.invoiceLines.edit">
				<FormattedMessage id="tutorials.texts.invoiceLines.edit" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultForm
				crudService={invoiceLineService}
				// navConfig={buildOneLevelNestedRelationNavConfig(invoiceLines, invoiceId)}
				navConfig={invoices}
				itemId={itemId}
				namespace={namespace}
				initialValues={initialValues}
				validationSchema={invoiceLineValidation}
				// backButtonUrl={invoices.href}
				prefilledFormValues={{
					invoice_id: invoiceId
				}}
			>
				<InvoiceLineFormFields/>
			</DefaultForm>
		</DefaultEditPage>;
	}
};
