import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import DefaultCreatePage from "../shared/crud/DefaultCreate";
import {equipmentRepairs} from "../../configurations/navigation";
import DefaultForm from "../shared/crud/DefaultForm";
import EquipmentRepairFormFields, {equipmentRepairValidation} from "./FormFields";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

export default
@inject("equipmentRepairService")
@observer
class EquipmentRepairCreatePage extends Component {
	render() {
		const namespace = "equipmentRepairCreatePage";
		const {equipmentRepairService} = this.props;

		return <DefaultCreatePage
			crudService={equipmentRepairService}
			titleId="components.equipmentRepairs.create.title"
			crumbs={[equipmentRepairs]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.equipmentRepairs.create">
				<FormattedMessage id="tutorials.texts.equipmentRepairs.create" values={formattingFunctions} />
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions} />
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions} />
			</PageTutorial>
			<DefaultForm
				crudService={equipmentRepairService}
				navConfig={equipmentRepairs}
				namespace={namespace}
				initialValues={{}}
				validationSchema={equipmentRepairValidation}>
				<EquipmentRepairFormFields/>
			</DefaultForm>
		</DefaultCreatePage>;
	}
}
