import React, {Component} from "react";
import {Breadcrumb} from "antd";
import PropTypes from "prop-types";
import {Link} from "react-router-dom";
import {FormattedMessage} from "react-intl";

export default class DefaultBreadcrumbs extends Component {
	static propTypes = {
		crumbs: PropTypes.array.isRequired
	};

	static defaultProps = {
		crumbs: []
	};

	render() {
		const crumbs = this.props.crumbs;
		const last = crumbs.pop();

		return <Breadcrumb>
			{crumbs.map(crumb => {
				return <Breadcrumb.Item key={crumb.href}>
					<Link to={crumb.href}>
						{crumb.titleId && <FormattedMessage id={crumb.titleId}/>}
						{crumb.title ? crumb.title : ""}
					</Link>
				</Breadcrumb.Item>;
			})}
			{last.titleId && <Breadcrumb.Item><FormattedMessage id={last.titleId}/></Breadcrumb.Item>}
			{last.title && <Breadcrumb.Item>{last.title}</Breadcrumb.Item>}
		</Breadcrumb>;
	}
}
