import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import {invoices} from "../../configurations/navigation";
import DefaultEditPage from "../shared/crud/DefaultEdit";
import InvoiceFormFields, {invoiceValidation} from "./FormFields";
import DefaultForm from "../shared/crud/DefaultForm";
import {paddedNumberFormatter} from "../../base/utilities";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";

export default
@inject("invoiceService", "counterpartyService")
@withRouter
@observer
class InvoiceEditPage extends Component {
	namespace = "invoiceEditPage";

	render() {
		const namespace = this.namespace;
		const itemId = parseInt(this.props.match.params.invoiceId, 10);
		const {invoiceService, counterpartyService} = this.props;
		const record = invoiceService.item[namespace];

		let initialValues = {};
		let titleParams = {};

		if (record) {
			initialValues = {
				number: record.number,
				date: record.date,
				counterparty_id: record.counterparty_id,
				totalPaymentAdditional: record.totalPaymentAdditional
			};
			titleParams = {
				name: paddedNumberFormatter(record.number)
			};
		}

		return <DefaultEditPage
			crudService={invoiceService}
			titleId="components.invoices.edit.title"
			titleParams={titleParams}
			itemId={itemId}
			crumbs={[invoices]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.invoices.edit">
				<FormattedMessage id="tutorials.texts.invoices.edit" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultForm
				crudService={invoiceService}
				navConfig={invoices}
				itemId={itemId}
				namespace={namespace}
				initialValues={initialValues}
				validationSchema={invoiceValidation}
			>
				<InvoiceFormFields
					crudService={invoiceService}
					counterpartyService={counterpartyService}
					namespace={namespace}
				/>
			</DefaultForm>
		</DefaultEditPage>;
	}
}
