import React, {Component} from 'react';
import {observer} from "mobx-react";
import {Select} from "antd";
import {FormattedMessage, injectIntl} from "react-intl";
import {FormItemDatePicker, FormItemInput, FormItemInputNumber, FormItemSelect} from "../../base/components/inputs";
import PropTypes from "prop-types";
import CounterpartyService from "../../services/CounterpartyService";
import {defaultErrorHandler} from "../../base/errors/defaultErrorHandler";
import {FlexCol, FlexRow} from "../../base/components/layouts/FlexGrid";
import * as Yup from "yup";
import {rules} from "../../base/utilities/forms";

export const invoiceValidation = Yup.object().shape({
	number: rules.required,
	date: rules.required,
	counterparty_id: rules.required
});

export default @injectIntl
@observer
class InvoiceFormFields extends Component {
	static propTypes = {
		counterpartyService: PropTypes.instanceOf(CounterpartyService).isRequired,
		namespace: PropTypes.string.isRequired,
	};

	componentDidMount() {
		const {namespace, counterpartyService} = this.props;

		counterpartyService.createNamespace(namespace);
		this.reloadCounterparties();
	}

	componentWillUnmount() {
		this.props.counterpartyService.resetNamespaceData(this.props.namespace);
	}

	render() {
		const {namespace, counterpartyService} = this.props;

		return <>
			<FlexRow>
				<FlexCol>
					<FormItemInputNumber
						id="number"
						label={<FormattedMessage id="components.invoices.number"/>}
					/>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol>
					<FormItemDatePicker
						id="date"
						label={<FormattedMessage id="components.invoices.date"/>}
					/>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol>
					<FormItemSelect
						id="counterparty_id"
						loading={!counterpartyService.itemList[namespace]}
						label={<FormattedMessage id="components.invoices.counterparty"/>}
						showSearch
						placeholder={this.props.intl.formatMessage({id: "select.placeholder.default"})}
						defaultActiveFirstOption={false}
						onSearch={this.handleCounterpartySelectSearch}
						filterOption={false}
					>
						{counterpartyService.itemList[namespace] && counterpartyService.itemList[namespace].map(
							counterparty => <Select.Option key={counterparty.id} value={counterparty.id}>{counterparty.company}</Select.Option>
						)}
					</FormItemSelect>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol>
					<FormItemInput
						id="totalPaymentAdditional"
						label={<FormattedMessage id="components.invoices.totalPaymentAdditional"/>}
					/>
				</FlexCol>
			</FlexRow>
		</>;
	}

	handleCounterpartySelectSearch = async (value) => {
		const {namespace, counterpartyService} = this.props;
		counterpartyService.setSearch({
			fields: ["company"],
			value
		}, namespace);

		this.reloadCounterparties();
	};

	reloadCounterparties = async () => {
		const {namespace, counterpartyService} = this.props;

		try {
			await counterpartyService.fetchItemList(namespace);
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.get", e);
		}
	};
};
