import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import {Redirect, Route, withRouter} from "react-router-dom";
import PropTypes from "prop-types";
import DefaultLoadingPage from "../data-display/DefaultLoadingPage";

@inject('context')
@withRouter
@observer
class PrivateRoute extends Component {
	static propTypes = {
		component: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
		authenticatedLayout: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
		render: PropTypes.func,
		loginUrl: PropTypes.string,
	};

	static defaultProps = {
		loginUrl: "/login"
	};

	render() {

		const {component: Cmp, context: appContext, render, loginUrl, authenticatedLayout: AuthenticatedLayout, ...rest} = this.props;

		const {isLoading, isAuthenticated} = appContext.appService;

		return <Route {...rest} render={(props) => {
			if (isAuthenticated !== true && isLoading === true) {
				return <DefaultLoadingPage/>;
			}

			if (isAuthenticated === true) {
				let contents;

				if (Cmp) {
					contents = <Cmp {...props} {...rest} />;
				}

				if (render) {
					contents = render({...props, ...rest});
				}

				if (AuthenticatedLayout) {
					return <AuthenticatedLayout {...props}>{contents}</AuthenticatedLayout>;
				}
				return contents;
			} else {
				return <Redirect to={{
					pathname: loginUrl,
					state: {
						from: this.props.location.pathname
					}
				}}/>;
			}
		}}/>;
	}
}

export default PrivateRoute;
