import React, {Component} from 'react';
import {observer} from "mobx-react";
import {Button, Icon, message, Typography} from "antd";
import {FormattedMessage, injectIntl} from "react-intl";
import {withRouter} from "react-router-dom";
import PropTypes from "prop-types";
import CrudService from "../../../base/services/CrudService";
import DefaultLoadingPage from "../../../base/components/data-display/DefaultLoadingPage";
import {defaultErrorHandler} from "../../../base/errors/defaultErrorHandler";
import {Form, Formik} from "formik";
import {FlexCol, FlexRow} from "../../../base/components/layouts/FlexGrid";

@withRouter
class InnerForm extends Component {
	static propTypes = {
		navConfig: PropTypes.object,
		backButtonUrl: PropTypes.string,
	};

	static defaultProps = {};

	render() {
		return <Form>
			{this.props.children}
			<FlexRow className="mt-5">
				<FlexCol xs="auto">
					<Button onClick={this.handleBackButtonClick}>
						<Icon type="left"/>
						<Typography.Text><FormattedMessage id="back"/></Typography.Text>
					</Button>
				</FlexCol>
				<FlexCol className="text-right">
					<Button type="primary" htmlType="submit">
						<Icon type="save"/>
						<Typography.Text style={{color: "white"}}><FormattedMessage id={"submit"}/></Typography.Text>
					</Button>
					<Button type="danger" style={{marginLeft: 8}} onClick={this.handleReset}>
						<Icon type="undo"/>
						<Typography.Text style={{color: "white"}}><FormattedMessage id={"reset"}/></Typography.Text>
					</Button>
				</FlexCol>
			</FlexRow>
		</Form>;
	}

	handleReset = () => {
		this.props.resetForm();
	};

	handleBackButtonClick = () => {
		const backButtonUrl = this.props.backButtonUrl;
		const navConfig = this.props.navConfig;

		this.props.history.push(`${backButtonUrl ? backButtonUrl : navConfig.href}`, this.props.match);
	};
}

/**
 *  Default form wrapper, providing boilerplate functionality.
 */
@withRouter
@injectIntl
@observer
class DefaultForm extends Component {

	static propTypes = {
		crudService: PropTypes.instanceOf(CrudService).isRequired,
		navConfig: PropTypes.object,
		initialValues: PropTypes.object.isRequired,
		validationSchema: PropTypes.object.isRequired,
		itemId: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
		namespace: PropTypes.string.isRequired,
		prefilledFormValues: PropTypes.object,
		redirectAfterSubmit: PropTypes.func,
		backButtonUrl: PropTypes.string,
	};

	static defaultProps = {
		prefilledFormValues: {},
	};

	render() {
		const {namespace, crudService, initialValues, validationSchema, navConfig, backButtonUrl} = this.props;
		let record = crudService.item[namespace];

		if (!record) {
			return <DefaultLoadingPage/>;
		}
		return <Formik
			enableReinitialize
			initialValues={initialValues}
			validationSchema={validationSchema}
			onSubmit={this.handleSubmit}>
			{(form) => <InnerForm {...form} navConfig={navConfig} backButtonUrl={backButtonUrl}>
				{this.props.children}
			</InnerForm>}
		</Formik>;
	}

	handleSubmit = async (values, {setSubmitting}) => {
		const {namespace, prefilledFormValues, crudService, navConfig, redirectAfterSubmit} = this.props;
		setSubmitting(false);
		try {
			let id = this.props.itemId;

			if (id) {
				await crudService.save({id, ...prefilledFormValues, ...values}, namespace);
			} else {
				id = await crudService.save({...prefilledFormValues, ...values}, namespace);
			}

			if (redirectAfterSubmit) {
				redirectAfterSubmit({id});
			} else {
				// this.props.history.replace("/reload");
				// this.props.history.replace(`${navConfig.href}/${id}`);
				this.props.history.replace(`${navConfig.href}`);
			}
			message.success(this.props.intl.formatMessage({id: "application.success.save"}));
		} catch (e) {
			defaultErrorHandler(this.props, "application.error.save", e);
		}
	};
}

export default DefaultForm;
