if (!Intl.PluralRules) {
	require('@formatjs/intl-pluralrules/polyfill');
	require('@formatjs/intl-pluralrules/dist/locale-data/de');
	require('@formatjs/intl-pluralrules/dist/locale-data/en');
	require('@formatjs/intl-pluralrules/dist/locale-data/bg');
}

if (!Intl.RelativeTimeFormat) {
	require('@formatjs/intl-relativetimeformat/polyfill');
	require('@formatjs/intl-relativetimeformat/dist/locale-data/de');
	require('@formatjs/intl-relativetimeformat/dist/locale-data/en');
	require('@formatjs/intl-relativetimeformat/dist/locale-data/bg');
}
