import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {Icon, Layout, Select, Tooltip, Typography} from "antd";
import digidWorksLogo from "../../base/assets/images/digid-works-logo.png";
import {DefaultFooter} from "../../base/components/layouts/DefaultFooter";
import DefaultMenu from "../../base/components/menus/DefaultMenu";
import {FormattedMessage, injectIntl} from "react-intl";
import DefaultLogoutButton from "../../base/components/auth/DefaultLogoutButton";
import {inject, observer} from "mobx-react";
import {availableCurrencies, availableLocales} from "../../localizations";
import {FlexCol, FlexRow} from "../../base/components/layouts/FlexGrid";
import TutorialTip from "../../base/components/data-display/TutorialTip";

/**
 * Default layout.
 */
@inject("context")
@injectIntl
@observer
class MenusLayout extends Component {
	constructor(props) {
		super(props);

		this.state = {
			collapsed: false
		};
	}

	render() {
		const {collapsed} = this.state;
		const {intl} = this.props;

		return <Layout style={{minHeight: "100%"}}>
			<Layout.Sider
				collapsedWidth="0"
				trigger={null}
				collapsible
				collapsed={collapsed}
			>
				<Link style={{display: "block"}} to="/">
					<Typography.Text style={{color: "#74fe38", fontSize: "3rem", fontFamily: "Mansalva", lineHeight: 1}}>
						<FormattedMessage id="application.title"/>
					</Typography.Text>
					<div style={{textAlign: "right"}}>
						<Typography.Text strong style={{color: "white", fontSize: "1.2rem", paddingRight: "5px"}}>
							<FormattedMessage id="by"/>
						</Typography.Text>
						<img style={{maxWidth: "120px"}}
							 src={digidWorksLogo} title={intl.formatMessage({id: "application.digid.logo.title"})} alt={intl.formatMessage({id: "application.digid.logo.title"})}/>
					</div>
				</Link>
				<DefaultMenu menuProps={{
					style: {
						marginTop: "20px"
					}
				}}/>
			</Layout.Sider>
			<Layout>
				<Layout.Header>
					<FlexRow>
						<FlexCol className="d-flex">
							<Tooltip title={<FormattedMessage id="tooltips.sidebarMenuTrigger"/>}>
								<Icon
									type={collapsed ? 'menu-unfold' : 'menu-fold'}
									onClick={this.handleSideMenuTriggerClick}
									style={{
										fontSize: "25px",
										alignSelf: "center",
									}}
								/>
							</Tooltip>
						</FlexCol>
						<FlexCol className="d-flex ml-auto" xs="auto">
							<TutorialTip title={<FormattedMessage id="tooltips.websiteCurrency"/>} placement="bottom">
								<Select className="borderless-select"
										defaultValue={this.props.context.appService.settings.currency}
										onChange={this.handleCurrencySelect}
										dropdownMatchSelectWidth={false}>
									{Object.entries(availableCurrencies).map(([key, value]) => <Select.Option key={key} value={key}><FormattedMessage id={value}/></Select.Option>)}
								</Select>
							</TutorialTip>
						</FlexCol>
						<FlexCol className="d-flex" xs="auto">
							<TutorialTip title={<FormattedMessage id="tooltips.websiteLocale"/>} placement="bottom">
								<Select className="borderless-select"
										defaultValue={this.props.context.appService.settings.locale}
										onChange={this.handleLocaleSelect}
										dropdownMatchSelectWidth={false}>
									{Object.entries(availableLocales).map(([key, value]) => <Select.Option key={key} value={key}><FormattedMessage id={value}/></Select.Option>)}
								</Select>
							</TutorialTip>
						</FlexCol>
						<FlexCol className="d-flex" xs="auto">
							<TutorialTip title={<FormattedMessage id="tooltips.logoutButton"/>} placement="bottomRight">
								<DefaultLogoutButton style={{fontSize: "25px", alignSelf: "center",}}
													 title={this.props.intl.formatMessage({id: "logout"})}/>
							</TutorialTip>
						</FlexCol>
					</FlexRow>
				</Layout.Header>
				<Layout.Content style={{padding: "2rem"}}>
					{this.props.children}
				</Layout.Content>
				<DefaultFooter/>
			</Layout>
		</Layout>
	}

	handleSideMenuTriggerClick = () => {
		this.setState({collapsed: !this.state.collapsed});
	};

	handleCurrencySelect = (value) => {
		this.props.context.appService.changeSetting("currency", value);
	};

	handleLocaleSelect = (value) => {
		this.props.context.appService.changeSetting("locale", value);
	};
}

export default MenusLayout;
