import React from 'react';
import {Layout} from "antd";
import PropTypes from "prop-types";

/**
 * Default layout footer.
 */
export const DefaultFooter = (props) => {
	return <Layout.Footer style={{textAlign: 'center'}}>{props.message}</Layout.Footer>;
};

DefaultFooter.propTypes = {
	message: PropTypes.node
};

DefaultFooter.defaultProps = {
	message: <span>Created with {"\u2764"} by <a href="https://digidworks.com" target="_blank" rel="noopener noreferrer">DigidWorks</a>© {new Date().getFullYear()}</span>
};
