import React, {Component} from 'react';
import {inject, observer} from "mobx-react";
import {FormattedMessage, injectIntl} from "react-intl";
import {rules} from "../../base/utilities/forms";
import {FormItemDatePicker, FormItemInput, FormItemInputNumber} from "../../base/components/inputs";
import {FlexCol, FlexRow} from "../../base/components/layouts/FlexGrid";
import * as Yup from "yup";
import {intToCurrencyFormatter, intToCurrencyParser} from "../../base/utilities";

export const equipmentRepairValidation = Yup.object().shape({
	description: rules.required,
	date: rules.required,
	cost: rules.required,
});

export default
@inject("context")
@injectIntl
@observer
class EquipmentRepairFormFields extends Component {
	render() {
		return <>
			<FlexRow>
				<FlexCol>
					<FormItemInput
						id="description"
						label={<FormattedMessage id="components.equipmentRepairs.description"/>}
					/>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol>
					<FormItemDatePicker
						id="date"
						label={<FormattedMessage id="components.equipmentRepairs.date"/>}
					/>
				</FlexCol>
				<FlexCol>
					<FormItemInputNumber
						id="cost"
						label={<FormattedMessage id="components.equipmentRepairs.cost"/>}
						formatter={value => this.props.intl.formatNumber(intToCurrencyFormatter(value), {format: "decimal"})}
						parser={value => intToCurrencyParser(value, this.props.context.appService.settings.locale)}
						addonBefore={this.props.context.appService.settings.currency}
					/>
				</FlexCol>
			</FlexRow>
		</>;
	}
};
