import React, {Component} from "react";
import {observer} from "mobx-react";
import PropTypes from "prop-types";
import CrudService from "../../services/CrudService";
import DefaultSpinner from "./DefaultSpinner";

export default
@observer
class RelationField extends Component {

	static propTypes = {
		crudService: PropTypes.instanceOf(CrudService).isRequired,
		namespace: PropTypes.string.isRequired,
		itemKey: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
		displayProp: PropTypes.string.isRequired
	};

	render() {
		const {displayProp, itemKey, namespace, crudService} = this.props;
		const itemMap = crudService.itemMap[namespace];
		if (!itemMap || !itemMap[itemKey]) {
			return <DefaultSpinner/>;
		}

		return itemMap[itemKey][displayProp];
	}
};
