import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import DefaultCreatePage from "../shared/crud/DefaultCreate";
import {users} from "../../configurations/navigation";
import DefaultForm from "../shared/crud/DefaultForm";
import UserFormFields, {userValidation} from "./FormFields";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";
import {rules} from "../../base/utilities/forms";

export default
@inject("userService")
@observer
class UserCreatePage extends Component {
	render() {
		const namespace = "userCreatePage";
		const {userService} = this.props;

		return <DefaultCreatePage
			crudService={userService}
			titleId="components.users.create.title"
			crumbs={[users]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.users.create">
				<FormattedMessage id="tutorials.texts.users.create" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultForm
				crudService={userService}
				navConfig={users}
				namespace={namespace}
				initialValues={{}}
				validationSchema={userValidation.shape({password: rules.required})}>
				<UserFormFields/>
			</DefaultForm>
		</DefaultCreatePage>;
	}
}
