import React, {Component} from "react";
import {inject, observer} from "mobx-react";
import {withRouter} from "react-router-dom";
import {users} from "../../configurations/navigation";
import DefaultEditPage from "../shared/crud/DefaultEdit";
import DefaultForm from "../shared/crud/DefaultForm";
import UserFormFields, {userValidation} from "./FormFields";
import {FormattedMessage} from "react-intl";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";
import {toJS} from "mobx";

export default
@inject("userService")
@withRouter
@observer
class UserEditPage extends Component {
	render() {
		const namespace = "userEditPage";
		const itemId = parseInt(this.props.match.params.userId, 10);
		const {userService} = this.props;
		let record = userService.item[namespace];

		let initialValues = {};
		let titleParams = {name: ""};

		if (record) {
			initialValues = {
				optedOutTutorials: toJS(record.optedOutTutorials),
				role: record.role,
				username: record.username,
			};

			titleParams = {
				name: record.username
			};
		}

		return <DefaultEditPage
			crudService={userService}
			titleId="components.users.edit.title"
			titleParams={titleParams}
			itemId={itemId}
			crumbs={[users]}
			namespace={namespace}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.users.edit">
				<FormattedMessage id="tutorials.texts.users.edit" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultForm
				crudService={userService}
				navConfig={users}
				itemId={itemId}
				namespace={namespace}
				initialValues={initialValues}
				validationSchema={userValidation}>
				<UserFormFields user={record}/>
			</DefaultForm>
		</DefaultEditPage>;
	}
}
