import React, {Component} from 'react';
import {observer} from "mobx-react";
import {FormattedMessage} from "react-intl";
import {rules} from "../../base/utilities/forms";
import {FormItemClear, FormItemInput, FormItemSelect} from "../../base/components/inputs";
import {FlexCol, FlexRow} from "../../base/components/layouts/FlexGrid";
import * as Yup from "yup";
import {Select} from "antd";
import {rolesList} from "../../configurations/roles";

export const userValidation = Yup.object().shape({
	username: rules.required,
});

export default @observer
class UserFormFields extends Component {
	render() {
		return <>
			<FlexRow>
				<FlexCol>
					<FormItemInput
						id="username"
						label={<FormattedMessage id="components.users.username"/>}
					/>
				</FlexCol>
				<FlexCol>
					<FormItemInput
						id="password"
						label={<FormattedMessage id="components.users.password"/>}
					/>
				</FlexCol>
			</FlexRow>
			<FlexRow>
				<FlexCol>
					<FormItemSelect
						id="role"
						label={<FormattedMessage id="components.users.role"/>}
						dropdownMatchSelectWidth={false}
					>
						{Object.entries(rolesList).map(([key, value]) => <Select.Option key={key} value={key}><FormattedMessage id={value}/></Select.Option>)}
					</FormItemSelect>
				</FlexCol>
				<FlexCol>
					<FormItemClear
						id="optedOutTutorials"
						label={<FormattedMessage id="components.users.clearOptedOutTutorials"/>}
						clearedValue={[]}
					/>
				</FlexCol>
			</FlexRow>
		</>;
	}
};
