import {inject, observer} from "mobx-react";
import React, {Component} from "react";
import {buildOneLevelNestedRelationNavConfig, invoiceLines, invoices} from "../../configurations/navigation";
import {FormattedMessage, FormattedNumber} from "react-intl";
import DefaultListPage from "../shared/crud/DefaultList";
import DefaultTable from "../shared/crud/DefaultTable";
import {withRouter} from "react-router-dom";
import {intToCurrencyFormatter} from "../../base/utilities";

export default
@inject("context", "invoiceLineService")
@withRouter
@observer
class InvoiceLineListPage extends Component {
	render() {
		const namespace = "invoiceLineListPage";
		const service = this.props.invoiceLineService;
		const invoiceId = parseInt(this.props.match.params.invoiceId, 10);
		const titleId = "components.invoiceLines.list.title";
		const searchFields = [
			"description",
			"measure",
			"quantity",
			"unitPrice",
		];
		const currency = this.props.context.appService.settings.currency;
		const columns = [
			{
				title: () => <FormattedMessage id="components.invoiceLines.description"/>,
				dataIndex: "description",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.invoiceLines.measure"/>,
				dataIndex: "measure",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.invoiceLines.quantity"/>,
				dataIndex: "quantity",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.invoiceLines.unitPriceNoVat"/>,
				dataIndex: "unitPrice",
				sorter: true,
				render: (value, record) => <FormattedNumber value={intToCurrencyFormatter(value)} format={currency}/>
			},
		];

		return <DefaultListPage
			titleId={titleId}
			crumbs={[invoices]}
		>
			<DefaultTable
				namespace={namespace}
				navConfig={buildOneLevelNestedRelationNavConfig(invoiceLines, invoiceId)}
				crudService={service}
				columns={columns}
				searchFields={searchFields}
			/>
		</DefaultListPage>;
	}
}
