import React, {Component} from "react";
import {Button, Card, Icon, message} from 'antd';
import {inject, observer} from "mobx-react";
import {Redirect, withRouter} from "react-router-dom";
import {defaultErrorHandler} from "../../errors/defaultErrorHandler";
import PropTypes from "prop-types";
import {FormattedMessage, injectIntl} from "react-intl";
import {FormItemInput} from "../inputs";
import DefaultLoadingPage from "../data-display/DefaultLoadingPage";
import {FlexCol, FlexRow} from "../layouts/FlexGrid";
import digidWorksLogo from "../../assets/images/digid-works-logo.png";
import {Form, Formik} from "formik";
import * as Yup from 'yup';
import {rules} from "../../utilities/forms";
import {formattingFunctions} from "../../localizations";

export default
@inject("context")
@injectIntl
@withRouter
@observer
class DefaultLoginPage extends Component {
	static propTypes = {
		redirectUrl: PropTypes.string,
	};

	static defaultProps = {
		redirectUrl: "/"
	};

	render() {
		const service = this.props.context.appService;

		if (service.isAuthenticated) {
			return <Redirect to={this.props.redirectUrl}/>;
		}

		if (service.isLoading) {
			return <DefaultLoadingPage/>;
		}

		const validationSchema = Yup.object().shape({
			username: rules.emailRequired,
			password: rules.required,
		});

		return (
			<div className="v-h-centered-container">
				<Card title={<FlexRow>
					<FlexCol xs="auto">
						<FormattedMessage id="login"/>
					</FlexCol>
					<FlexCol className="d-flex">
						<img style={{maxWidth: "120px", marginLeft: "auto"}}
							 src={digidWorksLogo} title={this.props.intl.formatMessage({id: "application.digid.logo.title"})}
							 alt={this.props.intl.formatMessage({id: "application.digid.logo.title"})}/>
					</FlexCol>
				</FlexRow>}
					  bordered={false}
					  style={{
						  maxWidth: "450px"
					  }}
				>
					<FormattedMessage id="loginPage.text" values={{...formattingFunctions, appName: this.props.intl.formatMessage({id: "application.meta.title"})}}/>
					<Formik
						initialValues={{}}
						validationSchema={validationSchema}
						onSubmit={this.handleSubmit}
					>
						<Form>
							<FlexRow className="mb-2">
								<FlexCol>
									<FormItemInput
										id="username"
										placeholder={this.props.intl.formatMessage({id: "components.login.username"})}
										size="large"
										prefix={<Icon type="user"/>}
									/>
								</FlexCol>
							</FlexRow>
							<FlexRow className="mb-3">
								<FlexCol>
									<FormItemInput
										id="password"
										placeholder={this.props.intl.formatMessage({id: "components.login.password"})}
										size="large"
										type="password"
										prefix={<Icon type="lock"/>}
									/>
								</FlexCol>
							</FlexRow>
							<FlexRow>
								<FlexCol>
									<Button size="large" block type="primary" htmlType="submit"><FormattedMessage id="login"/></Button>
								</FlexCol>
							</FlexRow>
						</Form>
					</Formik>
				</Card>
			</div>
		);
	}

	handleSubmit = async (values, {setSubmitting}) => {
		const service = this.props.context.appService;
		setSubmitting(false);
		try {
			await service.login(values.username, values.password);

			this.props.history.push(this.props.redirectUrl);
		} catch (e) {
			if (e.isUnauthorized) {
				message.error(this.props.intl.formatMessage({id: "errors.badCredentials"}));
				return;
			}
			defaultErrorHandler(this.props, "application.error.login", e);
		}
	};
};
