import {inject} from "mobx-react";
import React, {Component} from "react";
import {users} from "../../configurations/navigation";
import {FormattedMessage} from "react-intl";
import DefaultListPage from "../shared/crud/DefaultList";
import DefaultTable from "../shared/crud/DefaultTable";
import {formattingFunctions} from "../../base/localizations";
import PageTutorial from "../../base/components/tutorials/PageTutorial";
import {rolesList} from "../../configurations/roles";

export default
@inject("userService")
class UserListPage extends Component {
	render() {
		const namespace = "userListPage";
		const service = this.props.userService;
		const titleId = "components.users.list.title";
		const searchFields = [
			"username",
			"role",
		];
		const columns = [
			{
				title: () => <FormattedMessage id="components.users.username"/>,
				dataIndex: "username",
				sorter: true,
			},
			{
				title: () => <FormattedMessage id="components.users.role"/>,
				dataIndex: "role",
				sorter: true,
				render: (val) => <FormattedMessage id={rolesList[val]}/>
			},
		];

		return <DefaultListPage
			titleId={titleId}
		>
			<PageTutorial namespace={namespace} titleId="tutorials.titles.users.list">
				<FormattedMessage id="tutorials.texts.users.list" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.listPage" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.submitAndResetButtons" values={formattingFunctions}/>
				<FormattedMessage id="tutorials.texts.navigateSidebarMenu" values={formattingFunctions}/>
			</PageTutorial>
			<DefaultTable
				namespace={namespace}
				navConfig={users}
				crudService={service}
				columns={columns}
				searchFields={searchFields}
			/>
		</DefaultListPage>;
	}
}
