import {Tooltip} from "antd";
import React, {Component} from "react";

export default class TutorialTip extends Component {

	constructor(props) {
		super(props);

		this.state = {
			visible: false
		};
	}

	componentDidMount() {
		document.addEventListener("mousedown", this.handleDocumentClick);
	}

	componentWillUnmount() {
		document.removeEventListener("mousedown", this.handleDocumentClick);
	}

	render() {
		return <Tooltip
			ref={this.setTooltipRef}
			visible={this.state.visible}
			onVisibleChange={this.handleVisibleChange}
			{...this.props} />;
	}

	setTooltipRef = (node) => {
		this.tooltipRef = node;
	};

	handleDocumentClick = (event) => {
		if (this.tooltipRef && this.tooltipRef.getPopupDomNode && this.tooltipRef.getPopupDomNode() && !this.tooltipRef.getPopupDomNode().contains(event.target)) {
			this.setState({visible: false});
		}
	};

	handleVisibleChange = (visible) => {
		this.setState({visible});
	};
}
