import localforage from "localforage";
import BaseService from "./BaseService";
import {merge} from "lodash";

/**
 * Service for saving data to client's local storage.
 */
class DefaultLocalStorageService extends BaseService {

	/**
	 * Loads session from browser storage.
	 * @returns {Promise<void>}
	 */
	load = async (sessionKey) => {
		this.loading(`localStorage:load:${sessionKey}`);

		const sessionData = await localforage.getItem(sessionKey);

		this.loaded(`localStorage:load:${sessionKey}`);
		return sessionData;
	};

	/**
	 * Saves session to browser storage.
	 * @returns {Promise<void>}
	 */
	save = async (sessionKey, data) => {
		this.loading(`localStorage:save:${sessionKey}`);

		const result = await localforage.setItem(sessionKey, data);

		this.loaded(`localStorage:save:${sessionKey}`);

		return result;
	};

	/**
	 * Saves session to browser storage.
	 * @returns {Promise<void>}
	 */
	update = async (sessionKey, data) => {
		this.loading(`localStorage:update:${sessionKey}`);

		const sessionData = await localforage.getItem(sessionKey);
		const result = await localforage.setItem(sessionKey, merge({}, sessionData, data));

		this.loaded(`localStorage:update:${sessionKey}`);

		return result;
	};

	/**
	 * Delete value from browser storage.
	 * @param key
	 * @returns {Promise<void>}
	 */
	delete = async (key) => {
		this.loading(`localStorage:delete:${key}`);

		const result = await localforage.removeItem(key);

		this.loaded(`localStorage:delete:${key}`);

		return result;
	};

	/**
	 * Return all keys from browser storage.
	 */
	keys = async () => {
		this.loading(`localStorage:keys`);

		const result = await localforage.keys();

		this.loaded(`localStorage:keys`);

		return result;
	};

	/**
	 * Iterate over all values in browser storage.
	 */
	iterate = (iteratorCallback) => {
		return localforage.iterate(iteratorCallback);
	};
}

export default DefaultLocalStorageService;
