import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

const FlexContainer = (props) => {
	const {className, fluid, ...rest} = props;
	return <div className={classNames({"container": !fluid, "container-fluid": fluid, [className]: className})} {...rest} />;
};

FlexContainer.propTypes = {
	className: PropTypes.string,
	fluid: PropTypes.bool,
};

const FlexRow = (props) => {
	const {className, gutters, ...rest} = props;
	return <div className={classNames("row", className, {"no-gutters": !gutters})} {...rest} />;
};

FlexRow.propTypes = {
	className: PropTypes.string,
	gutters: PropTypes.bool,
};

FlexRow.defaultProps = {
	gutters: true,
};

const FlexCol = (props) => {
	const {
		xs, sm, md, lg, xl, xxl, xxxl, xxxxl,
		offsetXs, offsetSm, offsetMd, offsetLg, offsetXl, offsetXxl, offsetXxxl, offsetXxxxl,
		className, ...rest
	} = props;

	return <div className={classNames({
		"col": !xs,
		[`col-${xs}`]: xs,
		[`col-sm-${sm}`]: sm,
		[`col-md-${md}`]: md,
		[`col-lg-${lg}`]: lg,
		[`col-xl-${xl}`]: xl,
		[`col-xxl-${xxl}`]: xxl,
		[`col-xxxl-${xxxl}`]: xxxl,
		[`col-xxxxl-${xxxxl}`]: xxxxl,
		[`offset-${offsetXs}`]: offsetXs,
		[`offset-sm-${offsetSm}`]: offsetSm,
		[`offset-md-${offsetMd}`]: offsetMd,
		[`offset-lg-${offsetLg}`]: offsetLg,
		[`offset-xl-${offsetXl}`]: offsetXl,
		[`offset-xxl-${offsetXxl}`]: offsetXxl,
		[`offset-xxxl-${offsetXxxl}`]: offsetXxxl,
		[`offset-xxxxl-${offsetXxxxl}`]: offsetXxxxl,
		[className]: className,
	})} {...rest} />;
};

FlexCol.propTypes = {
	className: PropTypes.string,
	xs: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	sm: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	md: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	lg: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	xl: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	xxl: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	xxxl: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	xxxxl: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	offsetXs: PropTypes.oneOfType([PropTypes.number]),
	offsetSm: PropTypes.oneOfType([PropTypes.number]),
	offsetMd: PropTypes.oneOfType([PropTypes.number]),
	offsetLg: PropTypes.oneOfType([PropTypes.number]),
	offsetXl: PropTypes.oneOfType([PropTypes.number]),
	offsetXxl: PropTypes.oneOfType([PropTypes.number]),
	offsetXxxl: PropTypes.oneOfType([PropTypes.number]),
	offsetXxxxl: PropTypes.oneOfType([PropTypes.number]),
};

export {
	FlexContainer,
	FlexRow,
	FlexCol
};
